<template>
    <div v-show="!wrongTF" @click="() => {
        this.$router.push({ path: '/boards' });
    }" style="
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      position: relative;
      left: 0%;
      translate: 0% 200%;
    ">
        <div class="iconDelete" style="background-size: 100px 100px; width: 100px; height: 100px"></div>
        <div style="font-size: 25px">Boards를 통해 접속해주세요</div>
    </div>
    <article id="boardInnerAll" v-show="wrongTF" v-if="userInfo">
        <header id="boardInnerAllHeader">
            <div id="boardInnerAllHeaderRow1">
                <h4 :title="`created by ${userInfo.userName}`">{{ boardInfo.boardName }}</h4>
                <b :title="`created on ${boardInfo.boardCreated}`"
                    class="boardInnerAllHeaderRowButton boardInnerAllHeaderRowButtonBack off" @click="() => {
                        this.$router.push({ path: '/boards' });
                    }"
                    v-show="boardInnerAllSectionTF">Back</b>
                <b  v-if="boardInnerAllHeaderRowButtonEditTF"
                    :title="`click to toggle edit mode`"
                    class="boardInnerAllHeaderRowButton boardInnerAllHeaderRowButtonEdit"                     
                    @click="callPageAdjustDone"
                    v-show="boardInnerAllSectionTF">Done</b>
                <b :title="`click to toggle edit mode`"
                    class="boardInnerAllHeaderRowButton boardInnerAllHeaderRowButtonEdit" 
                    :class="boardInnerAllHeaderRowButtonEditTF?'on':'off'"
                    v-if="tokenData.userPower>=3"
                    @click="callPageAdjust"
                    v-show="boardInnerAllSectionTF">Edit</b>
                    <b :title="`click to write new article`"
                    class="boardInnerAllHeaderRowButton boardInnerAllHeaderRowButtonBack off"                     
                    v-if="tokenData.userNo != null"
                    @click="callPageAdd"
                    v-show="boardInnerAllSectionTF">Add</b>
            </div>
            <div id="boardInnerAllHeaderRow2">
                <span :title="`${boardInfo.boardExpressionTerm}`">{{ boardInfo.boardExpression }}</span>
            </div>
        </header>
        <section id="boardInnerAllSection"
        :class="boardInnerAllHeaderRowButtonEditTF?'edit':''"
        @click="clickSection"
        v-show="boardInnerAllSectionTF"
        >
            <p class="boardInnerAllSectionP">

            </p>
        </section>
        <section id="boardInnerAllSectionAdd"
        v-if="boardInnerAllSectionAddTF && !boardInnerAllSectionTF"
        >
            <boardInnerAllSectionAdd
                :boardInfo="boardInfo"
                :tokenData="tokenData"
                @informOut="defaultSection"
            ></boardInnerAllSectionAdd>
        </section>

        <footer id="boardInnerAllFooter" v-show="boardInnerAllSectionTF">
            <div id="boardInnerAllFooterRow1">
                <div id="boardInnerAllFooterNavL" @click="clickFooter">&lt;&nbsp;</div>
                <div id="boardInnerAllFooterNavM" @click="clickFooter">
                    <span></span>
                </div>
                <div id="boardInnerAllFooterNavR" @click="clickFooter">&nbsp;&gt;</div>
            </div>
            <div id="boardInnerAllFooterRow2">
                <input type="text" name="naving" id="boardInnerAllFooterNavingInput" placeholder="pages"
                    v-model="tryPage" @blur="checkNumber(tryPage)" />
                <button type="button" id="boardInnerAllFooterNavingButton" @click="pathing(tryPage)">
                    이동
                </button>
            </div>
        </footer>
    </article>
</template>

<script>
import { mapState, mapActions } from "vuex";
import mathing from "@/services/mathing";
import boardStyleBackground from '@/datas/backgroundSource.json'
import DataService from "@/java/service/DataService";
import ArticleService from "@/java/service/ArticleService.js";
import TokenService from "@/services/token/TokenService";

import boardInnerAllSectionAdd from "@/views/underViews/articleCreate.vue"
export default {
    components :{
        boardInnerAllSectionAdd
    },
    data() {
        return {
            boardStyle: null,
            firstTry: 0,
            boardInfo: JSON.parse(localStorage.getItem('boardInfo')) || null,
            userInfo: null,
            currentBoardNo: this.$route.params.no,
            wrongTF: false,
            boardStyleBackground: boardStyleBackground.boardStyleBackground.map((bg, index) => {
                if (index === 0) return ''; // 첫 번째 항목은 빈 문자열로 유지
                return require(`@/assets/background/${bg}`);
            }),
            eachPage: 10,
            tryPage: 1,
            currentPage: 1,
            indexNumbers: 0,
            datas: null,
            emptyBoard: {
                articleNo: 0,
                articleTitle: "아무 글도 없습니다.",
                articleContent: "아무 내용도 없습니다.",
                articleCreated: "1999-01-01T01:01:01",
                articleAdjusted: null
            },
            tokenData: {
                userPower: 0,
            },
            boardInnerAllHeaderRowButtonEditTF : false,
            boardInnerAllSectionTF : true,
            boardInnerAllSectionAddTF:false,
        }
    },
    computed: {
        ...mapState(["token"]),
        signedToken() {            
            return this.token;
        },
        
    },
    created() {
        this.setUserDef();
    },
    watch: {
        signedToken(newToken) {            
            if (newToken === null) {
                this.refreshTokenVuex();
            } else {
                this.tokenData = TokenService.decodeToken().data;                
            }
        },
    },
    mounted() {
        if (this.signedToken === null) {
            this.refreshTokenVuex();
        } else {
            this.tokenData = TokenService.decodeToken().data;            
        }
        
        if (this.boardInfo === null && this.firstTry < 4) {
            this.tryFetchBoardInfo();
        }
        if (this.boardInfo != null) {
            if (this.boardInfo.boardNo != this.currentBoardNo) {

            } else {
                this.wrongTF = true;
                this.setDef()
            }
        }

    },
    methods: {
        ...mapActions(["refreshTokenVuex", "setPopUpMsg", "setBoardInfo"]),
        defaultSection(){
            this.setDef();
        },
        tryFetchBoardInfo() {
            const storedBoardInfo = JSON.parse(localStorage.getItem('boardInfo'));
            this.boardInfo = storedBoardInfo;
            this.firstTry++;
            if (this.boardInfo == null && this.firstTry < 4) {
                this.tryFetchBoardInfo();
            }
        },
        async setDef(dir=null) {
            this.boardInnerAllHeaderRowButtonEditTF = false;
            this.boardInnerAllSectionTF = true;
            this.boardInnerAllSectionAddTF=false;
            if (JSON.parse(this.boardInfo.boardStyle) != null && JSON.parse(this.boardInfo.boardStyle) != '') {
                let tempStyle = JSON.parse(this.boardInfo.boardStyle)
                if (tempStyle.background != null && tempStyle.background != '') {
                    document.documentElement.style.setProperty('--backgroundCustomImage', `url(${this.boardStyleBackground[tempStyle.background]})`);
                } else {
                    document.documentElement.style.setProperty('--backgroundCustomImage', `url(${this.boardStyleBackground[0]})`);
                }
            } else {
                document.documentElement.style.setProperty('--backgroundCustomImage', `url(${this.boardStyleBackground[0]})`);
            }
            await this.readArticles(dir);
            await this.indexing();
            await this.pathing(1);
        },
        async setUserDef() {
            try {
                if (this.boardInfo != null && this.boardInfo.boardWhoNo>0) {
                    let result = await DataService.claimUserNo(this.boardInfo.boardWhoNo);
                    if (result.status == 200) {
                        this.userInfo = result.data;
                    }
                }
            } catch (e) {
                console.error(e);

            }
        },
        async readArticles(dir=null) {
            let res = dir;
            if(dir==null){
                res = await ArticleService.readArticles(this.boardInfo);
            }            
            await mathing.delay(500);
            try {
                if (res.status == 204) {//빈 것
                    this.datas = []
                    this.datas.push(this.emptyBoard)
                } else if (res.status == 200) { //뭔가 있는 것                    
                    this.datas = res.data
                    this.datas.forEach((item, index) => {
                        item.currentNo = index + 1;
                    })
                } else {//고장난것
                    this.wrongTF = false;
                }
            } catch (e) {
                console.error(e)
                this.wrongTF = false;
            }

        },
        async indexing() {
            if (this.boardInfo == null) {
                return;
            }
            this.indexNumbers = Math.ceil(this.boardInfo.articleCount/10);
            if (this.indexNumbers == 0) { this.indexNumbers = 1 };
            const box = document.getElementById('boardInnerAllFooterNavM');
            box.innerHTML = "";
            for (let i = 1; i <= this.indexNumbers; i++) {
                const newSpan = document.createElement("span");
                newSpan.innerText = i;
                box.appendChild(newSpan);
            }

        },
        async pathing(page = 1) {
            console.log(this.datas)
            this.currentPage = page;
            const box = document.getElementById('boardInnerAllSection');
            box.innerHTML = "";
            let datas = this.datas;
            const start = (page - 1) * this.eachPage;
            const end = start + this.eachPage;
            const cuttedData = datas.slice(start, end);

            cuttedData.forEach((item) => {
                const newP = document.createElement('p')
                newP.className = "boardInnerAllSectionP";
                newP.setAttribute('data-articleno', item.articleNo != null ? item.articleNo : 0)
                newP.innerHTML = `
                    <span class="articleNo" data-locations="sectionP">${item.currentNo != null ? item.currentNo : 0}</span>
                    <div class="articleTitle" data-locations="sectionP"><span>${item.articleTitle}</span></div>
                    <span class="articleCreated" data-locations="sectionP">${item.articleCreated}</span>
                
                `;
                box.appendChild(newP);

            });
            const footerNavM = document.getElementById("boardInnerAllFooterNavM");
            Array.from(footerNavM.children).forEach((child) => {
                child.classList.remove("currentFooter");
            });
            footerNavM.children[page - 1].classList.add("currentFooter");
        },
        clickFooter(e) {
            if (e.target.tagName === "SPAN") {
                this.pathing(e.target.textContent);
                return;
            }
            if (e.target.id === "boardInnerAllFooterNavR") {
                if (this.currentPage + 1 <= this.indexNumbers) {
                    this.pathing(this.currentPage + 1);
                }
                return;
            }
            if (e.target.id === "boardInnerAllFooterNavL") {
                if (this.currentPage - 1 >= 1) {
                    this.pathing(this.currentPage - 1);
                }
                return;
            }
        },
        checkNumber(newPage) {
            const numberRegex = /^[0-9]*$/;
            if (numberRegex.test(newPage)) {
                this.tryPage = newPage.replace(/^0+/, "");
                if (this.tryPage == 0) {
                    this.tryPage = 1;
                    return;
                }
                if (this.tryPage > this.indexNumbers) {
                    this.tryPage = this.indexNumbers;
                    return;
                }
            } else {
                this.tryPage = 1;
                return;
            }
        },
        callPageAdd(){
            this.boardInnerAllHeaderRowButtonEditTF = false;
            this.boardInnerAllSectionTF = false;
            this.boardInnerAllSectionAddTF=true;
        },
        callPageAdjust(){            
            if(!this.boardInnerAllHeaderRowButtonEditTF){
                this.boardInnerAllHeaderRowButtonEditTF = true;                
            }else{
                this.boardInnerAllHeaderRowButtonEditTF = false;                
            }
        },
        async callPageAdjustDone(){
            if(this.boardInnerAllHeaderRowButtonEditTF){
                let sectionPList = document.querySelectorAll('.boardInnerAllSectionP');
                let checked = [];
                sectionPList.forEach(item=>{
                    if(item.classList.contains('tagged')){
                        checked.push(item.dataset.articleno);
                    }
                })
                if(checked.length>0){
                    const res = await ArticleService.dumpArticles(this.boardInfo.boardNo,checked);
                    try {
                        this.boardInfo = JSON.parse(localStorage.getItem('boardInfo')) || null;
                        this.setDef(res)
                    } catch (error) {
                        
                    }
                    
                }
            }
        },
        clickSection(e){
            if(this.boardInnerAllHeaderRowButtonEditTF&&(e.target.parentElement.dataset.locations == "sectionP"||e.target.dataset.locations == "sectionP")&&(e.target.parentElement.dataset.articleno!=0)){
                e.target.parentElement.classList.toggle("tagged");
                return;
            }
            if(e.target.parentElement.dataset.locations == "sectionP"||e.target.dataset.locations == "sectionP"){
                let articleNo = e.target.closest(".boardInnerAllSectionP").dataset.articleno;
                if(articleNo!=0){
                    let a = String(this.boardInfo.boardNo).split('').map(digit =>  String.fromCharCode((parseInt(digit) % 26) + 97)).join('');
                    let b =String(articleNo).split('').map(digit =>  String.fromCharCode((parseInt(digit) % 26) + 97)).join('');
                    let c = a + "#" + b
                    console.log(c)
                    this.$router.push({name : 'article', params : {nums : c}})
                    console.log("준비완료 이동준비",articleNo)
                }
            }
        }
    }
}
</script>

<style>
:root {
    --header-font-size: 16px;
    --header-icon-height: 19px;
    --section-icon-height: 18px;
    --section-font-size: 17px;
}

body.body-boardInner {
    background-image: var(--backgroundCustomImage);

}

body.body-boardInner::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #30303000;
}

body.body-boardInner #routerDiv {
    background: transparent;
}

#boardInnerAll {
    position: relative;
    display: flex;
    width: 100%;
    min-height: var(--calculatedRouterDivHeight);
    flex-direction: column;
    justify-content: start;
    align-items: center;
    font-family: "GowunDodum";
    font-size: var(--header-icon-height);
    background: rgb(255, 248, 232);
    background: radial-gradient(circle, rgba(255, 248, 232, 0.9) 0%, rgba(255, 248, 232, 0.75) 98%);
    border-radius: var(--header-icon-height);
}

#boardInnerAll #boardInnerAllHeader {
    margin-top: var(--header-icon-height);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    width: 100%;
    flex-grow: 0;
}

#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow1 {
    display: flex;
    flex-direction: row;
    align-content: flex-end;
    justify-content: space-between;
    width: 100%;
}

#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow1>h4 {
    font-size: calc(var(--header-font-size) * 3);
    text-indent: calc(var(--header-font-size) * 3);
    font-weight: 800;
    flex-grow: 1;
}

#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow1>.boardInnerAllHeaderRowButton {
    border-radius: 0.5em;
    font-size: calc(var(--header-font-size) * 1);
    display: block;
    width: 2.3em;
    height: 2.3em;
    line-height: 2.3em;
    text-align: center;
    background-color: #2b2b2b;
    color: white;
    margin-right: 1em;
    user-select: none;
    cursor: pointer;
}

#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow1>.boardInnerAllHeaderRowButton.off:hover {
    transform-origin: center;
    animation: shake 0.5s ease forwards;
    color: yellow;
    transition: color 1s;
}
#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow1>.boardInnerAllHeaderRowButton.on{
    color: yellow;
    cursor:not-allowed;
}
#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow1>.boardInnerAllHeaderRowButton.on:hover {
    color: white;
    transition: color 1s;
}

#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow1>.boardInnerAllHeaderRowButtonBack:hover {
    transform-origin: center;
    animation: shake 0.5s ease forwards;
    color: rgb(0, 247, 255);
    transition: color 1s;
}

#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow2 {
    margin-top: var(--header-icon-height);
    margin-bottom: var(--header-icon-height);
    overflow: hidden;
    text-overflow: ellipsis;
    text-indent: calc(var(--header-font-size) * 2);
}

#boardInnerAll #boardInnerAllHeader>#boardInnerAllHeaderRow2>span {
    font-size: calc(var(--header-font-size) * 1);

}

#boardInnerAll #boardInnerAllSection {
    /* margin-top: calc(var(--header-icon-height));
    margin-bottom: calc(var(--header-icon-height)); */
    flex-grow: 1;
    /* background: #d2d2d2; */
    /* border: 3px solid #252525; */
    border-radius: 15px;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#boardInnerAll #boardInnerAllSection.edit>.boardInnerAllSectionP>.articleNo{
    transition: border 0.2s, background-color 1s;
    background-color: #e2e2e29c;
    border: 2px dotted black;
    border-radius: 3px;
}
#boardInnerAll #boardInnerAllSection.edit>.boardInnerAllSectionP.tagged>.articleNo{
    background-color: rgba(240, 226, 29, 0.842);
}
#boardInnerAll #boardInnerAllSection.edit>.boardInnerAllSectionP.tagged.terminated>.articleNo{
    background-color: rgba(36, 240, 29, 0.781);
}
#boardInnerAll #boardInnerAllSection>.boardInnerAllSectionP {
    flex-grow: 1;
    margin-bottom: calc(100% / 11 / 10);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background: rgba(238 255 248 / 69%);
    width: 98%;
    border-bottom: 3px solid black;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    font-size: var(--section-font-size);
    cursor: pointer;
    max-height: calc(var(--calculatedRouterDivHeight) / 13);
}

#boardInnerAll #boardInnerAllSection>.boardInnerAllSectionP:hover {
    transform: scale(1.005);
    transition: transform 0.1s;
    transform-origin: center;
}

#boardInnerAll #boardInnerAllSection>.boardInnerAllSectionP>span {
    display: inline-block;

}

#boardInnerAll #boardInnerAllSection>.boardInnerAllSectionP>.articleNo {
    margin-left: calc(var(--section-font-size) / 2);
    flex-grow: 0;
    padding: calc(var(--section-font-size) / 4);
    font-family: 'numbers';
    font-weight: 400;
}

#boardInnerAll #boardInnerAllSection>.boardInnerAllSectionP>.articleTitle {
    flex-grow: 1;
    text-indent: calc(var(--section-font-size) / 2);
    align-self: stretch;
    display: flex;
    align-items: center;
    overflow: hidden;
    text-overflow: ellipsis;

}

#boardInnerAll #boardInnerAllSection>.boardInnerAllSectionP>.articleTitle>span {
    display: inline-block;
}

#boardInnerAll #boardInnerAllSection>.boardInnerAllSectionP>.articleCreated {
    flex-grow: 0;
    font-size: calc(var(--section-font-size) * 0.8);
}
#boardInnerAll #boardInnerAllSectionAdd{    
    border-radius: 15px;
    width: 100%;
    position: relative;
    flex-grow: 1;
    display: flex;
}

#boardInnerAll #boardInnerAllFooter {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    margin-bottom: 5px;
    flex-grow:0;
}

#boardInnerAll #boardInnerAllFooter>#boardInnerAllFooterRow1 {
    display: flex;
    flex-direction: row;
}

#boardInnerAll #boardInnerAllFooter>#boardInnerAllFooterRow2 {}

#boardInnerAll>#boardInnerAllFooter>#boardInnerAllFooterRow1>#boardInnerAllFooterNavL,
#boardInnerAll>#boardInnerAllFooter>#boardInnerAllFooterRow1>#boardInnerAllFooterNavR {
    font-family: "thickHighlight";
    cursor: pointer;
    font-size: calc(var(--header-icon-height) / 1);
}

#boardInnerAll>#boardInnerAllFooter>#boardInnerAllFooterRow1>#boardInnerAllFooterNavM {
    background-color: transparent;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

#boardInnerAll>#boardInnerAllFooter>#boardInnerAllFooterRow1>#boardInnerAllFooterNavM>span {
    font-family: 'numbers';
    font-weight: 400;
    display: inline-block;
    color: #3e3e3e;
    min-width: fit-content;
    text-align: center;
    margin-left: calc(var(--header-icon-height) / 4);
    margin-right: calc(var(--header-icon-height) / 4);
    user-select: none;
    cursor: pointer;
    font-size: calc(var(--header-icon-height) / 1.1);
}

#boardInnerAll>#boardInnerAllFooter>#boardInnerAllFooterRow1>#boardInnerAllFooterNavM>span.currentFooter {
    color: #5a5a5a;
    font-weight: 700;
    background: #ffffffaf;
    width: 1em;
    border-radius: 50%;
}

#boardInnerAll>#boardInnerAllFooter>#boardInnerAllFooterRow2 {
    margin-top: calc(var(--header-icon-height) / 4);
    display: flex;
    flex-direction: row;
    height: calc(var(--header-icon-height) * 1.5);
}

#boardInnerAll>#boardInnerAllFooter>#boardInnerAllFooterRow2>#boardInnerAllFooterNavingInput {
    aspect-ratio: 2/1;
    display: block;
    height: 100%;
    text-align: center;
    background: #ffffff63;
    border: none;
    margin-right: calc(var(--header-icon-height) * 0.5);
    box-shadow: #333 1px 1px 3px 1px;
    border-radius: 5px;
}

#boardInnerAll>#boardInnerAllFooter>#boardInnerAllFooterRow2>#boardInnerAllFooterNavingButton {
    aspect-ratio: 2/1;
    display: inline-block;
    height: 100%;
    text-align: center;
    background: #ffffff63;
    border-radius: 5px;
    box-shadow: #333 1px 1px 3px 1px;
    cursor: pointer;
}
</style>