
import {createStore} from 'vuex'

import TokenService from '@/services/token/TokenService';


export default new createStore({
    state : {
        routes : [],
        // initialized : false,
        token : null,
        domSize : null,
        popUpMsg : null,
        boardInfo : null,
    },
    mutations : {
        setRoutes(state, routes){
            state.routes = routes;
        },
        setInitialized(state){
            state.initialized = true;
        },
        setToken(state,token){
            state.token = token;
        },
        clearToken(state){
            state.token = null;
        },
        setDomSize(state,domSize){
            state.domSize = domSize;
        },
        setPopUpMsg(state,msg){
            state.popUpMsg = msg;
        },
        setBoardInfo(state,boardInfo){
            state.boardInfo=boardInfo;
        }
    },
    actions : {
        // initializeApp({commit, state}){
        //     if(!state.initialized){
        //         console.log("최초 페이지 on 로그아웃")
        //         TokenService.removeToken();
        //         commit('setInitialized')
        //     }
        // },
        refreshTokenVuex({commit}){
            let localDataToken = TokenService.getToken();            
            if(localDataToken!=null){
                commit('setToken',localDataToken);
            }
        },
        signIn({commit},token){
            if(token != null){
                commit('setToken',token);
            }
            
        },
        signOut({commit}){
            commit('clearToken')
        },
        callDom({commit, state},domSize){            
            if(domSize!=null && state.domSize!=domSize){
                commit('setDomSize',domSize)
            }
        },
        setPopUpMsg({commit},popUpMsg){
            if(popUpMsg==null){
                commit('setPopUpMsg',null)
            }else{
                commit('setPopUpMsg',popUpMsg)    
            }
            
        },
        setBoardInfo({commit},boardInfo){
            if(boardInfo==null){
                commit('setBoardInfo',null)
            }else{
                commit('setBoardInfo',boardInfo)
            }
        },


    }
})

// 1. Vuex 개념 설명
// Actions: 비동기 작업(예: API 호출)을 처리하고, mutations를 호출하여 상태(state)를 변경합니다. 첫번째 매개변수는 context object. 이 객체에는 commit, dispath, state, rootState 등이 들어가며 두 번째 매개변수에 playload가 들어감.
// Mutations: Vuex 상태를 변경하는 유일한 방법으로, 동기 작업을 처리합니다. 항상 첫 매개변수는 현재상태state를 참조. 두 번째 매개변수는 playload 전달되는 데이터.
// State: Vuex 저장소에서 관리되는 상태입니다.
// Getters: Vuex 상태의 계산된 속성을 반환합니다.
// 2. Vuex와 관련된 구문 설명
// mapActions: Vuex 액션을 컴포넌트 메서드로 바인딩할 때 사용합니다.
// mapState : Vuex의 state에 접근함. 이후 this.~ 로 호출해서 사용함. 
// ...mapState({
//     authToken: state => state.authToken 이렇게 
//   })
// commit: mutations를 호출할 때 사용합니다.
// dispatch: actions를 호출할 때 사용합니다.



