<template>
  <div id="monitorbox">
            <div id="monitorscreenTitle">
                CMD
            </div>
            <div id="monitorscreen">            
            </div>
            <div id="monitorscreenCMD">
                CMD
            </div>
        </div>
        <div id="keybox" @click="clickKeyboard">
            <div class="keyboard keyboard1">
                <div class="key">`</div>
                <div class="key">1</div>
                <div class="key">2</div>
                <div class="key">3</div>
                <div class="key">4</div>
                <div class="key">5</div>
                <div class="key">6</div>
                <div class="key">7</div>
                <div class="key">8</div>
                <div class="key">0</div>
                <div class="key">-</div>
                <div class="key">=</div>
                <div class="key keysp">←</div>
            </div>
            <div class="keyboard keyboard2">
                <div class="key key-q">Q</div>
                <div class="key key-w">W</div>
                <div class="key key-e">E</div>
                <div class="key key-r">R</div>
                <div class="key key-t">T</div>
                <div class="key key-y">Y</div>
                <div class="key key-u">U</div>
                <div class="key key-i">I</div>
                <div class="key key-o">O</div>
                <div class="key key-p">P</div>
                <div class="key">[</div>
                <div class="key">]</div>
                <div class="key">\</div>
            </div>
            <div class="keyboard keyboard3">
                <div class="key key-a">A</div>
                <div class="key key-s">S</div>
                <div class="key key-d">D</div>
                <div class="key key-f">F</div>
                <div class="key key-g">G</div>
                <div class="key key-h">H</div>
                <div class="key key-j">J</div>
                <div class="key key-k">K</div>
                <div class="key key-l">L</div>
                <div class="key">;</div>
                <div class="key">'</div>
                <div class="key keysp">enter</div>
            </div>
            <div class="keyboard keyboard4">
                <div class="key key-z">Z</div>
                <div class="key key-x">X</div>
                <div class="key key-c">C</div>
                <div class="key key-v">V</div>
                <div class="key keysp">space</div>            
                <div class="key key-b">B</div>
                <div class="key key-n">N</div>
                <div class="key key-m">M</div>
                <div class="key">,</div>
                <div class="key">.</div>
                <div class="key">/</div>            
            </div>
        </div>
</template>

<script>
import mathing from '@/services/mathing';
export default {
    data(){
        return{
            keyboardArray : [],
            // keyboardArrayOrigin : [],
            keyboardArrayOrigin: ['R', 'E', 'W', 'I', 'N', 'D','  ','p','r','o','j','e','c','t','\n','T','Y','P','E',' ','C','O','M','M','A','N','D'],
            keyboardArrayOnscreen : '',
            delayTime: 300,
            waits:false,
            waits2:false,
        }
    },
    computed() {
        
    },
    mounted() {
        this.displayInitialArray();
        
    },
    methods : {
        async displayInitialArray() {
            for (let i = 0; i < this.keyboardArrayOrigin.length; i++) {
                let a=null;
                this.waits = true; 
                const emailRegex = /^[a-zA-Z]$/;
                if(emailRegex.test(this.keyboardArrayOrigin[i])){
                    a = document.querySelector(`.key-${this.keyboardArrayOrigin[i].toLowerCase()}`);
                    a.style.border='inset';
                }
                this.keyboardArray.push(this.keyboardArrayOrigin[i]);
                if(i==this.keyboardArrayOrigin.length-1){
                    this.waits2 = true;   
                }
                await this.writeOnscreen(a);
                if(i==this.keyboardArrayOrigin.length-1){
                    this.keyboardArrayOnscreen += '\n';
                    monitorscreen.innerHTML = this.keyboardArrayOnscreen.replace(/\n/g, '<br/>') + '<span class="blinking-cursor">_</span>';                    
                }
            }
        },
        async writeOnscreen(a=null) {
            let monitorscreen = document.getElementById('monitorscreen');
            if (this.keyboardArray.length > 0) {
                this.keyboardArrayOnscreen += this.keyboardArray[0];
                await mathing.delay(this.delayTime);
                monitorscreen.innerHTML = this.keyboardArrayOnscreen.replace(/\n/g, '<br/>') + '<span class="blinking-cursor">_</span>';
                this.keyboardArray.splice(0, 1);
            } else {
                monitorscreen.innerHTML = this.keyboardArrayOnscreen.replace(/\n/g, '<br/>') + '<span class="blinking-cursor">_</span>';
            }
            if(a!=null){
                a.style.border='outset';
            }
            if(this.waits2){
                this.waits = false;
            }
        },
        async clickKeyboard(e) {
            if(this.waits){
                return;
            }
            let monitorscreen = document.getElementById('monitorscreen');
            if (e.target.classList.contains('key')) { 
                this.waits = true;               
                e.target.style.border = 'inset';
                if(e.target.classList.contains('keysp')){
                    switch(e.target.innerText){
                        case '←' :
                        if (this.keyboardArrayOnscreen.length > 0) {
                                this.keyboardArrayOnscreen = this.keyboardArrayOnscreen.slice(0, -1);   
                                monitorscreen.innerHTML = this.keyboardArrayOnscreen.replace(/\n/g, '<br/>') + '<span class="blinking-cursor">_</span>';                          
                            }
                        break;
                        case 'enter':
                        // this.keyboardArrayOnscreen += '\n';
                        // monitorscreen.innerHTML = this.keyboardArrayOnscreen.replace(/\n/g, '<br/>') + '<span class="blinking-cursor">_</span>';
                        // e.target.style.border = 'outset';
                        const lastLine = this.keyboardArrayOnscreen.split('\n').pop().trim();
                            this.processCommand(lastLine);
                            e.target.style.border = 'outset';
                            return;                                                    
                        
                        case 'space':
                            this.keyboardArray.push(' ')
                        break;
                    }
                }else{
                    this.keyboardArray.push(e.target.innerText);
                }
                await this.writeOnscreen();
                e.target.style.border = 'outset';
            }
        },
        processCommand(command) {
            console.log('Processing command:', command);
            // Add your command processing logic here
        }
    }

}
</script>

<style>
#keybox{
        margin-inline: auto;
            position: relative;
            min-width: 450px;
            max-width:720px;
            aspect-ratio: 500 / 200;
            background-color: rgb(236, 236, 228);
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            justify-content: space-around;
            border: inset;
            border-radius: 10px;
    align-content: space-around;
    transform-origin: bottom; /* 변환의 기준점을 아래로 설정 */
    transform: perspective(150px) rotateX(10deg);
        }
        .keyboard{
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            width: 90%;      
            height: 15%;      
        }
        .key{
            font-family: 'concert';
            font-size: 1.2em;
            line-height: 1.2em;
            cursor:pointer;
            user-select: none;
            width: 5%;            
            border: outset;
            margin-left: 1%;
            text-align: center;
            min-width: fit-content;
            background-color: white;
        }
        #monitorbox{            
            margin-inline: auto;
            min-width: 450px;
            max-width:720px;
            aspect-ratio: 1280 / 720;
            border: outset;
            background-color: rgb(27, 27, 27);
            border-radius: 15px;
        }
        #monitorscreen{
            width: 95%;
            padding: 20px;            
            margin-top: 1%;
            aspect-ratio: 1280 / 720;
            margin-inline: auto;
            background-color: rgb(0, 81, 255);            
            color: white;
            font-family: 'concert';
            border: inset;
            text-indent: 15px;
            font-size: 19px;
            line-height: 20px;
        }
        .blinking-cursor {
    animation: blink 1s step-end infinite;
}

@keyframes blink {
    from, to {
        visibility: hidden;
    }
    50% {
        visibility: visible;
    }
}
</style>