import http from '@/java/http-common';
import TokenService from '@/services/token/TokenService';

class DataService{

    async signIn(data) {
        try {
            let result = await http.post(`/auth/signin`, data);
            
            if (result.status === 200) {
               return TokenService.setNewToken(result);
            }else{
                return { status: 401, message: 'An error occurred during sign-in. UNAUTHORIZED' };
            }
            
        } catch (e) {
            console.error(e);
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }
    signOut(){
        if(localStorage.getItem('token') != null){
            return TokenService.removeToken();
        }else{
            let result = {
                status : 400
            }
            return result;
        }
        
    }
    async signOn(data){
        try {
            let result = await http.post(`/auth/signon`,data)
            if(result.status == 201){//HttpStatus.CREATED
                return result;
            }else{
                return { status: 400, message: 'An error occurred during sign-on.BAD REQUEST' };
            }
        } catch (error) {
            console.error(e);
            return { status: 500, message: 'An error occurred during sign-on. INTERNAL_SERVER_ERROR' };
        }
    }

    async claimUserNo(data){
        try {
            let result = await http.get(`/auth/claims/${data}`)
            console.log(result)
            if (result.status === 200) {
                return {status:200,data:result.data};
            }else{
                return { status: 401, message: 'An error occurred during sign-in. UNAUTHORIZED' };
            }
        } catch (e) {
            console.error(e);
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }

    async checkUserId(data){
        try {
            let result = await http.get(`/auth/check/${data}`)
            if (result.status === 200) {//OK
                return {status:200};
            }else if(result.status===226){//IM_USED
                return {status:226};
            }
        } catch (e) {
            console.error(e);
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }

    

}

export default new DataService();


