<template>  
  <div id="writingBox">
    <article id="articleTitle">      
      <div id="contextTitle" contenteditable="true">
        <p><br></p>
      </div>
    </article>
    <Contexts>

    </Contexts>
  </div>
</template>

<script>
import Contexts from './underViews/writingContext.vue'

import defaulting from '@/services/defaulting'
import mathing from '@/services/mathing'
import { mapState } from 'vuex'

export default {
  name: "writing",  
  components: {
    Contexts
  },
  data() {
    return {
      colorList: ["#FFD700", "#C0C0C0", "#B76E79", "#5AE1E1", "#39FF14", "#FFC0CB", "#E6E6FA", "#98FB98"],
    }
  },  
  computed:{
    ...mapState(['domSize']),
    // newDomSize(){
    //   console.log("computed")
    //   return this.domSize
    // }
  },
  mounted() {
    this.setDef()
  },
  watch : {
    domSize: {
      handler(newDom) {
        console.log("watched?", newDom);
        if (newDom) {
          this.setSize(newDom);
        }
      },
      deep: true // 객체 내부의 변화를 감지 > 없어도 되긴 해
    }
  },
  methods: {
    setDef() {
      this.setFocus();
      this.setColors();
      this.setSize(this.domSize)
    },
    setFocus(){
      let contenteditableBox = document.querySelector('#contextTitle')
      defaulting.setFocus(contenteditableBox);      
    },
    setColors(){      
      let rndTF = mathing.rndInt(0,1) >= 1 ? true : false;
      document.documentElement.style.setProperty('--random-color',`${defaulting.setColor[this.colorList]}`)
      rndTF ? document.documentElement.style.setProperty('--random-filter-grayscale',`grayscale(0.7)`) : document.documentElement.style.setProperty('--random-filter-grayscale',`grayscale(0.2)`);
    },
    setSize(newDom){
      console.log(newDom)
      document.documentElement.style.setProperty('--adjustedAspectRatio',`${newDom.domNavWidth * 0.8} / ${newDom.calculatedRouterDivHeight * 0.8}`)
    }
  },
}
</script>

<style>
:root{
  --adjustedAspectRatio : 10 / 5;
}
body.body-writing {
  background-color: var(--random-color);
  filter: var(--random-filter-grayscale);
}

nav.nav-writing {}

#writingBox {
  box-shadow: 5px 5px 5px black;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  top:10%;
  width: 90%;
  aspect-ratio: var(--adjustedAspectRatio);
  background: white;
}

#articleTitle {
  position: absolute;
  top: 10%;
  left: 0;
  
  color: black;
}
              @media (min-width: 1200px) {
                
              }
              @media (min-width: 700px) and (max-width: 1199px) {

              }
              @media (max-width: 699px) {
                #writingBox{
                  
                }
                
              }
</style>