<template>
    <div v-show="!boardAllCheckTF"  style="
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      position: relative;
      left: 0%;
      translate: 0% 200%;
    ">
        <div class="iconDelete" style="background-size: 100px 100px; width: 100px; height: 100px" @click="() => {
        this.$router.push({ path: '/' });
    }
        "></div>
        <div style="font-size: 25px">No Data!</div>
        <br>
        <div class="headerButton iconAdd" id="headerButtonAdd2" title="add new board" @click="callPageSave" style="background-size: contain; width: 35px; aspect-ratio: 192 / 224;"></div>
    </div>
    <article v-show="boardAllCheckTF" id="boardAllDiv">
        <header>
            <div id="headerFirstRow">
                <div class="headerButton" id="headerButtonRefresh" title="refresh" @click="refreshThis"></div>
                <div class="headerButton" id="headerButtonAdd" title="add new board" @click="callPageSave"
                    v-if="sortingOption.power > 0"></div>
                <div class="headerButton" id="headerButtonAdjust" title="adjust new board" @click="callPageAdjust"
                    v-if="sortingOption.power > 0"></div>
                <div class="headerButton" id="headerButtonAdjustConfirm" title="click to confirm"
                    v-if="boardAllBoardAdjustTF" @click="confirmPageAdjust"></div>
                <div class="headerButton" id="headerButtonOption" title="show sorting option" @click="showOptionBox">
                </div>
                <div class="headerOptionBox">
                    <div class="headerOptionBoxInner">
                        <div class="radioBorder"></div>
                        <input type="radio" name="sorting" id="sortingName" value="name" checked
                            @click="changeRadioOption" />
                        <span id="sortingNameSpan" data-direct="0" @click="changeRadioOption">1to9</span>
                    </div>
                    <div class="headerOptionBoxInner">
                        <div class="radioBorder"></div>
                        <input type="radio" name="sorting" id="sortingName" value="date" @click="changeRadioOption" />
                        <span id="sortingNameSpan" data-direct="0" @click="changeRadioOption">Newest to Oldest</span>
                    </div>
                    <div class="headerOptionBoxInner">
                        <div class="radioBorder"></div>
                        <input type="radio" name="sorting" id="sortingName" value="amount" @click="changeRadioOption" />
                        <span id="sortingNameSpan" data-direct="0" @click="changeRadioOption">Largest to Smallest</span>
                    </div>
                </div>
            </div>
            <div id="headerSecondRow">
                <div class="headerHelpBox">
                    <span class="headerHelpBoxIcon iconNew"></span>
                    <span class="headerHelpBoxSpan">최근 생성됨</span>
                </div>
                <div class="headerHelpBox">
                    <span class="headerHelpBoxIcon iconUpdated"></span>
                    <span class="headerHelpBoxSpan">최근 작성됨</span>
                </div>
                <div class="headerHelpBox">
                    <span class="headerHelpBoxIcon iconAdjust"></span>
                    <span class="headerHelpBoxSpan">수정하기</span>
                </div>
                <div class="headerHelpBox">
                    <span class="headerHelpBoxIcon iconDelete"></span>
                    <span class="headerHelpBoxSpan">삭제하기</span>
                </div>
            </div>
        </header>
        <section id="boardAllBoardSection" v-show="boardAllBoardSectionTF" @click="clickSection">
            <p class="sectionP">
                <span class="sectionP_no">0</span>
                <span class="sectionP_title">temptemptemp</span>
                <span class="sectionP_button sectionP_buttonOn iconNew"></span>
                <span class="sectionP_button sectionP_buttonOn iconUpdated"></span>
                <span class="sectionP_button sectionP_buttonOn iconAdjust"></span>
                <span class="sectionP_button sectionP_buttonOn iconDelete"></span>
            </p>
        </section>
        <section id="boardAllBoardSectionPlus" v-if="boardAllBoardSectionPlusTF">
            <boardAllBoardSectionPlus @informOut="closePageSave" :tokenData="tokenData"></boardAllBoardSectionPlus>
        </section>
        <section id="boardAllBoardSectionAdjust" v-if="boardAllBoardSectionAdjustTF">
            <boardAllBoardSectionAdjust @informOut="closePageSave" :tokenData="tokenData" :boardData="customBoardData">
            </boardAllBoardSectionAdjust>
        </section>
        <footer v-show="boardAllBoardSectionTF">
            <div id="footerNavFirstRow">
                <div id="footerNavL" @click="clickFooter">&lt;&nbsp;</div>
                <div id="footerNavM" @click="clickFooter">
                    <span></span>
                </div>
                <div id="footerNavR" @click="clickFooter">&nbsp;&gt;</div>
            </div>
            <div id="footerNavSecondRow">
                <input type="text" name="naving" id="footerNavingInput" placeholder="pages" v-model="tryPage"
                    @blur="checkNumber(tryPage)" />
                <button type="button" id="footerNavingButton" @click="pathing(tryPage)">
                    이동
                </button>
            </div>
        </footer>
    </article>
</template>

<script>
import DataService from "@/java/service/DataService";
import BoardService from "@/java/service/BoardService";
import TokenService from "@/services/token/TokenService";
import mathing from "@/services/mathing";
import boardAllBoardSectionPlus from "@/views/underViews/boardCreate.vue";
import boardAllBoardSectionAdjust from "@/views/underViews/boardAdjust.vue";
import { mapState, mapActions } from "vuex";

export default {
    components: {
        boardAllBoardSectionPlus,
        boardAllBoardSectionAdjust,
    },
    data() {
        return {
            sortingOption: {
                orders: "name",
                direct: 0,
                power: 0,
            },
            datas: [],
            tokenData: {
                userPower: 0,
            },
            boardAllCheckTF: false,
            boardAllBoardSectionTF: true,
            boardAllBoardSectionPlusTF: false,
            boardAllBoardSectionAdjustTF: false,
            boardAllBoardAdjustTF: false,
            eachPage: 10,
            currentPage: 1,
            tryPage: 1,
            indexNumbers: 0,
            selctedBoard: null,
            customBoardData: null,
            defDirection: null,
        };
    },

    computed: {
        ...mapState(["token"]),
        signedToken() {
            return this.token;
        },
    },
    watch: {
        '$route'(to, from) {
            this.boardAllCheckTF = false;
            if (to.path === '/admin-boards' && this.sortingOption.power==0) {
                this.sortingOption.power = 1;    
                this.setDef();            
            } else if (to.path === '/boards' && this.sortingOption.power==1) {
                this.sortingOption.power = 0;    
                this.setDef();            
            }        
            this.boardAllCheckTF = true;
        },
        signedToken(newToken) {
            if (newToken === null) {
                this.refreshTokenVuex();
            } else {
                this.tokenData = TokenService.decodeToken().data;
                console.log(this.tokenData);
            }
        },
    },
    mounted() {
        if (this.signedToken === null) {
            this.refreshTokenVuex();
        } else {
            this.tokenData = TokenService.decodeToken().data;
            console.log(this.tokenData);
        }
        const bodyClass = document.querySelector("body");
        if (bodyClass.classList.contains("body-board-all-admin")) {
            this.sortingOption.power = 1;
        } else {
            this.sortingOption.power = 0;
        }
        this.setDef();
    },
    methods: {
        ...mapActions(["refreshTokenVuex", "setPopUpMsg", "setBoardInfo"]),
        refreshThis() {
            this.setDef();
        },
        setDef() {
            localStorage.removeItem('boardInfo');
            this.closePageSave();
            this.readBoard();
            if (this.datas.length != 0) {
                this.sortingOption = this.checkRadio();
            }
        },
        showOptionBox() {
            const headerOptionBox = document.querySelector(".headerOptionBox");
            const headerOptionBoxSty = window.getComputedStyle(headerOptionBox);
            console.log(headerOptionBoxSty.transform);
            if (headerOptionBoxSty.transform === "matrix(0, 0, 0, 1, 0, 0)") {
                headerOptionBox.style.transform = "scaleX(100%)";
                return;
            }
            headerOptionBox.style.transform = "scaleX(0%)";
        },
        callPageAdjust() {
            const boardAllBoardSection = document.getElementById(
                "boardAllBoardSection"
            );
            if (!this.boardAllBoardAdjustTF) {
                this.boardAllBoardAdjustTF = true;
                boardAllBoardSection.classList.add("boardAllBoardAdjust");
            } else {
                this.boardAllBoardAdjustTF = false;
                boardAllBoardSection.classList.remove("boardAllBoardAdjust");
            }
        },
        async confirmPageAdjust() {
            const selectedBoxes = document.querySelectorAll(".tagged");
            let selectedBoxNodesToDelete = [];
            let selectedBoxNodesToLive = [];
            selectedBoxes.forEach((item) => {
                if (item.classList.contains("terminated")) {
                    selectedBoxNodesToLive.push(item.children[0].innerText);
                } else {
                    selectedBoxNodesToDelete.push(item.children[0].innerText);
                }
            });
            let res = null;
            if (selectedBoxNodesToDelete.length > 0) {
                for (let i = 0; i < selectedBoxNodesToDelete.length; i++) {
                    res = await BoardService.deleteBoard(
                        this.sortingOption,
                        selectedBoxNodesToDelete[i]
                    );
                }
                this.datas = res;
            }
            if (selectedBoxNodesToLive.length > 0) {
                for (let i = 0; i < selectedBoxNodesToLive.length; i++) {
                    res = await BoardService.liveBoard(
                        this.sortingOption,
                        selectedBoxNodesToLive[i]
                    );
                }
                this.datas = res;
            }
            await this.indexing();
            await this.pathing(this.currentPage);
        },
        callPageSave() {
            
            this.boardAllCheckTF = true;
            
            if (!this.boardAllBoardSectionTF) {
                this.closePageSave();
            } else {
                this.boardAllBoardSectionTF = false;
                this.boardAllBoardSectionPlusTF = true;
            }
        },
        closePageSave() {
            const boardAllBoardSection = document.getElementById(
                "boardAllBoardSection"
            );
            this.boardAllBoardSectionTF = true;
            this.boardAllBoardSectionPlusTF = false;
            this.boardAllBoardAdjustTF = false;
            this.boardAllBoardSectionAdjustTF = false;
            boardAllBoardSection.classList.remove("boardAllBoardAdjust");
        },
        changeRadioOption(e) {
            if (e.target.tagName == "INPUT") {
                this.sortingOption = this.checkRadio();
                this.readBoard();
                return;
            }
            let previousSibling = e.target.previousSibling;
            let dataSetName = previousSibling.value;
            if (e.target.dataset.direct == 0) {
                switch (dataSetName) {
                    case "name":
                        e.target.textContent = "9to1";
                        break;
                    case "date":
                        e.target.textContent = "Oldest to Newest";
                        break;
                    case "amount":
                        e.target.textContent = "Smallest to Largest ";
                        break;
                }
                e.target.dataset.direct = 1;
            } else {
                switch (dataSetName) {
                    case "name":
                        e.target.textContent = "1to9";
                        break;
                    case "date":
                        e.target.textContent = "Newest to Oldest";
                        break;
                    case "amount":
                        e.target.textContent = "Largest to Smallest";
                        break;
                }
                e.target.dataset.direct = 0;
            }
            console.log(this.sortingOption.orders, dataSetName);
            if (this.sortingOption.orders === dataSetName) {
                console.log("dddd");
                this.sortingOption = this.checkRadio();
                this.readBoard();
            }
        },
        clickFooter(e) {
            if (e.target.tagName === "SPAN") {
                this.pathing(e.target.textContent);
                return;
            }
            if (e.target.id === "footerNavR") {
                if (this.currentPage + 1 <= this.indexNumbers) {
                    this.pathing(this.currentPage + 1);
                }
                return;
            }
            if (e.target.id === "footerNavL") {
                if (this.currentPage - 1 >= 1) {
                    this.pathing(this.currentPage - 1);
                }
                return;
            }
        },
        clickSection(e) {
            if (
                e.target.dataset.locations == "sectionP" &&
                e.target.tagName === "SPAN"
            ) {
                if (
                    e.target.classList.contains("sectionP_no") ||
                    e.target.classList.contains("sectionP_title") ||
                    e.target.classList.contains("sectionP_buttonOn")
                ) {
                    let boardNo = e.target.closest(".sectionP").children[0].innerText;
                    let option = "watch";
                    if (e.target.classList.contains("sectionP_buttonOn")) {
                        let classList = [];
                        e.target.classList.forEach((key, value) => {
                            classList.push(key);
                        });
                        classList.splice(
                            classList.findIndex((r) => {
                                return r == "sectionP_button";
                            }),
                            1
                        );
                        classList.splice(
                            classList.findIndex((r) => {
                                return r == "sectionP_buttonOn";
                            }),
                            1
                        );
                        option = classList[0];
                    }
                    if(this.boardAllBoardAdjustTF){
                        option = "sectionP_no";
                        e.target.parentElement.classList.toggle("tagged");                        
                    }
                    if (
                        e.target.classList.contains("sectionP_no") &&
                        this.boardAllBoardAdjustTF
                    ) {
                        // e.target.parentElement.classList.toggle("tagged");                        
                    }
                    this.boardAction(boardNo, option);
                }
            }
        },
        async boardAction(boardNo, option) {
            let selctedBoard =
                this.datas.data[
                this.datas.data.findIndex((r) => {
                    return r.boardNo == boardNo;
                })
                ];
            switch (option) {
                case "iconAdjust":
                    this.customBoardData = selctedBoard;
                    this.boardAllBoardSectionAdjustTF = true;
                    this.boardAllBoardSectionTF = false;
                    break;
                case "iconDelete":
                    const res = await BoardService.deleteBoard(
                        this.sortingOption,
                        selctedBoard.boardNo
                    );
                    console.table(res);
                    if (res != null) {
                        this.datas = res;
                        await this.indexing();
                        await this.pathing(this.currentPage);
                    }
                    break;
                case "sectionP_no":
                    console.table("pNo", selctedBoard);
                    break;
                case "iconNew":
                case "iconUpdated":
                case "watch":
                default:
                    localStorage.setItem('boardInfo',JSON.stringify(selctedBoard))
                    console.log('test',selctedBoard.boardNo)
                    this.$router.push({name : 'board', params : {no: selctedBoard.boardNo}})
                    break;
            }
        },
        async readBoard() {
            const res = await BoardService.readAllBoard(this.sortingOption);
            console.log(res)
            if (res != null) {
                this.boardAllCheckTF = true;
            }
            await mathing.delay(500);
            if(res.status == 204){
                console.log("no data")
                this.boardAllCheckTF = false;
                return;
            }
            try {
                this.datas = res;
                this.sortingOption = this.checkRadio();
                await this.indexing();
                await this.pathing(1);
                console.table(this.tokenData);
                console.table(this.datas.data);
            } catch (error) {
                this.boardAllCheckTF = false;
                console.error(error);
            }
        },
        checkRadio() {
            let radioGroup = document.getElementsByName("sorting");
            for (var i = 0; i < radioGroup.length; i++) {
                if (radioGroup[i].checked) {
                    let object = {
                        orders: radioGroup[i].value,
                        direct: radioGroup[i].nextSibling.dataset.direct,
                        power : this.sortingOption.power
                    };
                    return object;
                }
            }
            return null;
        },
        async indexing() {
            this.indexNumbers = Math.ceil(this.datas.data.length / 10);
            if(this.indexNumbers==0){this.indexNumbers=1}
            let indexNumbers = this.indexNumbers;
            const box = document.getElementById("footerNavM");
            box.innerHTML = "";
            for (let i = 1; i <= indexNumbers; i++) {
                const newSpan = document.createElement("span");
                newSpan.innerText = i;
                box.appendChild(newSpan);
            }
            // if(this.currentPage==1){
            //     box.firstChild.classList.add('currentFooter')
            // }
        },
        checkNumber(newPage) {
            const numberRegex = /^[0-9]*$/;
            if (numberRegex.test(newPage)) {
                this.tryPage = newPage.replace(/^0+/, "");
                if (this.tryPage == 0) {
                    this.tryPage = 1;
                    return;
                }
                if (this.tryPage > this.indexNumbers) {
                    this.tryPage = this.indexNumbers;
                    return;
                }
            } else {
                this.tryPage = 1;
                return;
            }
        },
        async pathing(page) {
            this.currentPage = page;
            const box = document.getElementById("boardAllBoardSection");
            box.innerHTML = "";
            let datas = this.datas.data;
            console.log(box);
            const start = (page - 1) * this.eachPage;
            const end = start + this.eachPage;
            const cuttedData = datas.slice(start, end);

            cuttedData.forEach((item) => {
                let computedDate = new Date(item.boardCreated).getTime();
                let nowDate = Date.now();
                let threeDay = 259200000;
                const newP = document.createElement("p");
                newP.className = "sectionP";
                if (item.boardTerminated == 1) {
                    newP.classList.add("terminated");
                }
                if (this.tokenData.userPower >= 3) {
                    newP.innerHTML = `                    
                        <span class="sectionP_no" data-locations="sectionP">${item.boardNo
                        }</span>
                        <span class="sectionP_title" data-locations="sectionP" data-content="[${item.articleCount
                        }]">${item.boardName}</span>    
                        <span class="sectionP_button ${nowDate - computedDate <= threeDay
                            ? "sectionP_buttonOn"
                            : ""
                        } iconNew" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconUpdated" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconAdjust" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconDelete" data-locations="sectionP"></span>`;
                } else if (
                    this.tokenData.userPower < 3 &&
                    this.tokenData.userPower > 1 &&
                    this.tokenData.userNo == item.boardWhoNo
                ) {
                    newP.innerHTML = `                    
                        <span class="sectionP_no" data-locations="sectionP">${item.boardNo
                        }</span>
                        <span class="sectionP_title" data-locations="sectionP" data-content="[${item.articleCount
                        }]">${item.boardName}</span>    
                        <span class="sectionP_button ${nowDate - computedDate <= threeDay
                            ? "sectionP_buttonOn"
                            : ""
                        } iconNew" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconUpdated" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconAdjust" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconDelete" data-locations="sectionP"></span>`;
                } else if (
                    this.tokenData.userPower < 1 &&
                    this.tokenData.userNo == item.boardWhoNo
                ) {
                    newP.innerHTML = `                    
                        <span class="sectionP_no" data-locations="sectionP">${item.boardNo
                        }</span>
                        <span class="sectionP_title" data-locations="sectionP" data-content="[${item.articleCount
                        }]">${item.boardName}</span>    
                        <span class="sectionP_button ${nowDate - computedDate <= threeDay
                            ? "sectionP_buttonOn"
                            : ""
                        } iconNew" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconUpdated" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconAdjust" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconDelete" data-locations="sectionP"></span>`;
                } else {
                    newP.innerHTML = `                    
                        <span class="sectionP_no" data-locations="sectionP">${item.boardNo
                        }</span>
                        <span class="sectionP_title" data-locations="sectionP" data-content="[${item.articleCount
                        }]">${item.boardName}</span>    
                        <span class="sectionP_button ${nowDate - computedDate <= threeDay
                            ? "sectionP_buttonOn"
                            : ""
                        } iconNew" data-locations="sectionP"></span>
                        <span class="sectionP_button sectionP_buttonOn iconUpdated" data-locations="sectionP"></span>
                        <span class="sectionP_button iconAdjust" data-locations="sectionP"></span>
                        <span class="sectionP_button iconDelete" data-locations="sectionP"></span>`;
                }
                box.appendChild(newP);
            });
            const footerNavM = document.getElementById("footerNavM");
            Array.from(footerNavM.children).forEach((child) => {
                child.classList.remove("currentFooter");
            });
            footerNavM.children[page - 1].classList.add("currentFooter");
            this.closePageSave();
        },
    },
};
</script>

<style>
:root {
    --header-icon-height: 19px;
    --section-icon-height: 18px;
    --section-font-size: 17px;
}

body.body-board-all {
    background-image: url("@/assets/background/texture_paper.png");
}

body.body-board-all #routerDiv {
    background: transparent;
}

nav.nav-board-all .navDiv {
    background-color: #cdc9a5;
    border: 1px solid #8b4513;
}

nav.nav-board-all .navDiv>a {
    color: #654321;
}

nav.nav-board-all .navDiv .navArrow {
    color: #654321;
}

nav.nav-board-all .navDivChilds>a {
    color: #654321;
}

nav.nav-board-all .navDiv>span {
    color: #333;
}

body.body-board-all-admin {
    background-image: url("@/assets/background/Wallpaper_01.png");
}

body.body-board-all-admin #routerDiv {
    background: transparent;
}

nav.nav-board-all-admin .navDiv {
    background-color: #ffffd0;
    border: 1px solid #ffd700;
}

nav.nav-board-all-admin .navDiv>a {
    color: #8b4513;
}

nav.nav-board-all-admin .navDiv .navArrow {
    color: #ff7f50;
}

nav.nav-board-all-admin .navDivChilds>a {
    color: #2f4f4f;
}

nav.nav-board-all-admin .navDiv>span {
    color: #2f4f4f;
}

#boardAllDiv {
    position: relative;
    display: flex;
    width: 100%;
    height: var(--calculatedRouterDivHeight);
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-family: "GowunDodum";
    font-size: var(--header-icon-height);
}


#boardAllDiv input[type="radio"] {
    all: unset;
}

#boardAllDiv header {
    /* border: 3px solid #2525250a; */
    border-radius: 15px;
    width: 100%;
    padding: calc(var(--header-icon-height));
    margin-top: calc(var(--header-icon-height));
    display: flex;
    flex-direction: column;
    background: #d2d2d233;
}

#boardAllDiv header>#headerFirstRow {
    display: flex;
    flex-direction: row;
    height: calc(var(--header-icon-height) * 2.5);
    align-items: center;
}

#boardAllDiv header>#headerFirstRow .headerButton {
    margin-right: calc(var(--header-icon-height) * 1);
    height: 100%;
    aspect-ratio: 1/1;
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    cursor: pointer;
    /* box-shadow: black 0px 3px 3px 0px; */
}

#boardAllDiv header>#headerFirstRow #headerButtonRefresh {
    background-image: url("@/assets/icon/document_refresh.png");
}

#boardAllDiv header>#headerFirstRow #headerButtonAdd {
    background-image: url("@/assets/icon/document_plus.png");
}

#boardAllDiv header>#headerFirstRow #headerButtonAdjust {
    background-image: url("@/assets/icon/document_edit.png");
}

#boardAllDiv header>#headerFirstRow #headerButtonAdjustConfirm {
    background-image: url("@/assets/icon/document_delete.png");
    transform: scaleX(0);
    /* Initial scale */
    transform-origin: left;
    animation: expand 1s forwards;
}


#boardAllDiv header>#headerFirstRow #headerButtonOption {
    background-image: url("@/assets/icon/document_information.png");
}

#boardAllDiv header>#headerFirstRow .headerOptionBox {
    height: 90%;
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: row;
    background-color: #ffffff;
    padding-left: calc(var(--header-icon-height));
    padding-right: calc(var(--header-icon-height));
    border-top-right-radius: calc(var(--header-icon-height));
    transform-origin: left;
    transform: scaleX(0%);
    box-shadow: black 1px 1px 3px 0px;
    border: 3px solid black;
}

#boardAllDiv header>#headerFirstRow .headerOptionBox .headerOptionBoxInner {
    position: relative;
    display: flex;
    justify-content: start;
    align-items: center;
}

#boardAllDiv header>#headerFirstRow .headerOptionBox .headerOptionBoxInner>.radioBorder {
    display: inline-block;
    width: calc(var(--header-icon-height) + 5px);
    aspect-ratio: 1/1;
    background: transparent;
