<template>
  <div id="popUpSignOut">
      <div id="popUpSignOutBackground">
    
      </div>  
      <div id="popUpSignOutMsg">
            Successfully Signed Out!
      </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
    data(){
        return{

        }
    },
    created() {
        this.setDef()
    },
    computed : {
        popUpMsg(){
            return this.$store.state.popUpMsg;
        }
    },
    methods : {
        setDef(){
            const totalHeight = document.documentElement.scrollHeight;
            const viewHeight = window.innerHeight;
            const viewWidth = window.innerWidth;
            document.documentElement.style.setProperty('--total-height',`${totalHeight}px`);
            document.documentElement.style.setProperty('--adjusted-height',`${viewHeight * 0.3}px`);
            document.documentElement.style.setProperty('--adjusted-aspect-ratio',`${viewWidth * 0.8} / ${viewHeight * 0.6}`)
            if(this.popUpMsg!=null){
                document.getElementById('popUpSignOutMsg').innerText=this.popUpMsg.msg
            }
        }
    }
}
</script>

<style>
    :root{
        --width : 100vw;
    }
    #popUpSignOutBackground {
        width: var(--width);
        height: var(--total-height);
        opacity: 0.5;
        background: black;
        position: fixed;
        top:0;
        left:0;
        z-index: 100;
    }
    #popUpSignOutMsg{
        height: var(--adjusted-height);
        aspect-ratio: 4/3;
        position: fixed;
        top: 50%;
        left:50%;
        transform: translateX(-50%) translateY(-50%);
        background: aliceblue;
        z-index: 101;
    }


</style>