<template>
    <div id="boardCreateBoxCover" v-if="boardCreateBoxMsg != ''" @click="callDef"></div>
    <form id="boardCreateBox" @submit.prevent="handleSubmit">
        <div class="inputBox">
            <span @click="focus">게시판 제목</span>
            <input type="text" name="boardCreateBoxName" id="boardCreateBoxName" maxlength="45" v-model="boardCreateBoxName" required/>
            
        </div>
        <div class="inputBox">
            <span @click="focus">게시판 설명</span>
            <textarea type="text" name="boardCreateBoxExpression" id="boardCreateBoxExpression" maxlength="100" v-model="boardCreateBoxExpression"/>            
        </div>
        <div class="inputBox">
            <span>게시판 배경</span>
            <div id="backgroundBox">
                <div class="backgroundBoxInner backgroundBoxLeft" @click="changeBackgroundBox(-1)"></div>
                <div class="backgroundBoxInner backgroundBoxMiddle"></div>
                <div class="backgroundBoxInner backgroundBoxRight" @click="changeBackgroundBox(1)"></div>
            </div>
        </div>

        <div id="boardCreateBoxMsg" v-if="boardCreateBoxMsg != ''" @click="callDef">
            <div id="foldPage"></div>
            {{ boardCreateBoxMsg }}
        </div>
        <button type="button" id="boardCreateBoxButton1" @click="callOut">나가기</button>
        <button type="button" id="boardCreateBoxButton2" @click="callDef">초기화</button>
        <button type="button" id="boardCreateBoxButton3" @click="callSave">저장</button>
        <span id="boardCreateBoxNameSpan">&nbsp;</span>
        <span id="boardCreateBoxExpressionSpan">&nbsp;</span>
    </form>
     
</template>

<script>
import DataService from '@/java/service/DataService';
import BoardService from '@/java/service/BoardService';
import defaulting from '@/services/defaulting';
import mathing from '@/services/mathing';
import boardStyleBackground from '@/datas/backgroundSource.json'
export default {
    props:{
        tokenData : {
            type : Object,
            required : true,
        }
    },
    emits : [
        'informOut'
    ],
    data(){
        return{
            boardCreateBoxStyle :{
                background : 0,
            },
            boardCreateBoxName : '',
            boardCreateBoxExpression : '', 
            boardCreateBoxMsg : '글자abc2',
            boardCreateBoxNameSpan : null,
            boardCreateBoxExpressionSpan : null,
            boardStyleBackground: boardStyleBackground.boardStyleBackground.map((bg, index) => {
                if (index === 0) return ''; // 첫 번째 항목은 빈 문자열로 유지
                return require(`@/assets/background/${bg}`);
            }),
            
        }
    },
    watch : {
        boardCreateBoxName(input){
            if((input != null || input != '')){
                this.boardCreateBoxNameSpan.textContent = input;
                this.updateInputSize('boardCreateBoxName',this.boardCreateBoxNameSpan);
            }
        },
        // boardCreateBoxExpression(input){
        //     if((input != null || input != '')){
        //         this.boardCreateBoxExpressionSpan.textContent = input;
        //         this.updateInputSize('boardCreateBoxExpression',this.boardCreateBoxExpressionSpan);
        //     }
        // }
    },
    mounted() {
        this.callDef();
    },
    methods : {
        async callSave(){
            if(this.boardCreateBoxName.trim()!='' && this.boardCreateBoxName != null){
                let newObject = {
                    boardName : '',
                    boardExpression : '',
                    boardWhoNo:'',
                    boardStyle:''
                };
                newObject.boardName = this.boardCreateBoxName;
                newObject.boardExpression = this.boardCreateBoxExpression;
                newObject.boardWhoNo = this.tokenData.userNo;
                newObject.boardStyle=JSON.stringify(this.boardCreateBoxStyle)
                this.boardCreateBoxMsg = '저장중...';
                await mathing.delay(800);
                try {
                    const res = await BoardService.saveNewBoard(newObject);
                    if(res.status == 201){
                        this.boardCreateBoxMsg = '저장완료!';                        
                    }else{
                        this.boardCreateBoxMsg = '저장실패 :d';
                    }
                } catch (e) {
                    console.error(e);
                    this.boardCreateBoxMsg = '저장실패 :d';
                }
            }else{
                document.getElementById('boardCreateBoxName').focus()
            }   
    },
        callDef(){

            console.log(this.boardStyleBackground)
            this.boardCreateBoxName = '';
            this.boardCreateBoxExpression = '';
            this.boardCreateBoxMsg = '';
            this.boardCreateBoxExpressionSpan = document.getElementById('boardCreateBoxExpressionSpan');
            this.boardCreateBoxNameSpan = document.getElementById('boardCreateBoxNameSpan');
            const nameInputSty = window.getComputedStyle(document.getElementById('boardCreateBoxName'));
            const expressionInputSty = window.getComputedStyle(document.getElementById('boardCreateBoxExpression'));
            this.boardCreateBoxExpressionSpan.style.font = expressionInputSty.font;
            this.boardCreateBoxExpressionSpan.style.padding = expressionInputSty.padding;
            this.boardCreateBoxExpressionSpan.style.border = expressionInputSty.border;
            this.boardCreateBoxExpressionSpan.style.boxSizing = expressionInputSty.boxSizing;
            this.boardCreateBoxNameSpan.style.font = nameInputSty.font;
            this.boardCreateBoxNameSpan.style.padding = nameInputSty.padding;
            this.boardCreateBoxNameSpan.style.border = nameInputSty.border;
            this.boardCreateBoxNameSpan.style.boxSizing = nameInputSty.boxSizing;
            this.boardCreateBoxNameSpan.innerText=' '
            this.boardCreateBoxExpressionSpan.innerText=' '
            document.documentElement.style.setProperty('--backgroundCustomImageM',`url(${this.boardStyleBackground[this.boardCreateBoxStyle.background]})`);
            document.documentElement.style.setProperty('--backgroundCustomImageL',`url(${this.boardStyleBackground[this.boardCreateBoxStyle.background==0? this.boardStyleBackground.length-1:this.boardCreateBoxStyle.background - 1]})`);
            document.documentElement.style.setProperty('--backgroundCustomImageR',`url(${this.boardStyleBackground[this.boardCreateBoxStyle.background==this.boardStyleBackground.length-1? 0: this.boardCreateBoxStyle.background + 1]})`);
        },
        callOut(){
            this.$emit('informOut')
        },
        updateInputSize(id, spans){
            const input = document.getElementById(id);
            const span = spans;
            if(span.offsetWidth >= input.offsetWidth ){
                input.style.width = span.offsetWidth + 'px';
            }
            
        },
        focus(e){
            e.target.nextSibling.focus();
        },
        changeBackgroundBox(direction){
            let currentBackgroundNumber1 = this.boardCreateBoxStyle.background;
            let currentBackgroundNumber0 =0, currentBackgroundNumber2 =0;
            currentBackgroundNumber1 += direction;    
            currentBackgroundNumber0 = currentBackgroundNumber1 - 1;
            currentBackgroundNumber2 = currentBackgroundNumber1 + 1;        
            if(currentBackgroundNumber1 > this.boardStyleBackground.length-1 || currentBackgroundNumber1 == 0){
                currentBackgroundNumber0 = this.boardStyleBackground.length-1
                currentBackgroundNumber1 = 0;
                currentBackgroundNumber2 = 1;
            }
            if(currentBackgroundNumber1 == this.boardStyleBackground.length-1 || currentBackgroundNumber1 < 0){
                currentBackgroundNumber0 = this.boardStyleBackground.length-2
                currentBackgroundNumber1 = this.boardStyleBackground.length-1;
                currentBackgroundNumber2 = 0;
            }
            this.boardCreateBoxStyle.background = currentBackgroundNumber1            
            document.documentElement.style.setProperty('--backgroundCustomImageL',`url(${this.boardStyleBackground[currentBackgroundNumber0]})`);
            document.documentElement.style.setProperty('--backgroundCustomImageM',`url(${this.boardStyleBackground[currentBackgroundNumber1]})`);
            document.documentElement.style.setProperty('--backgroundCustomImageR',`url(${this.boardStyleBackground[currentBackgroundNumber2]})`);
            

        }
    },
        



}
</script>

<style>
:root{
    --create-font-size : 18px;
    --create-width-size : 210px;
}
#boardCreateBox{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#boardCreateBox>button{
    cursor: pointer;
}
.inputBox{
    display: flex;
    flex-direction: column;
    align-items: center;    
}
.inputBox>span{
    user-select: none;
    cursor:pointer;
}
#boardCreateBoxName{
    border: none;
    background: transparent;
    height: calc(var(--create-font-size) * 2);
    width: var(--create-width-size);
    /* text-indent: calc(var(--create-font-size) * 0.8); */
    font-size:var(--create-font-size);
    margin-top: calc(var(--create-font-size) / 3);
    margin-bottom: calc(var(--create-font-size) / 3);
}
#boardCreateBoxExpression{
    font-family: 'GowunDodum';
    border: none;
    background: transparent;
    font-size:var(--create-font-size);
    width: var(--create-width-size);
    aspect-ratio: 14 / 10;
    resize: none; /* 사용자가 크기를 조절할 수 없도록 설정 */
    white-space: pre-wrap; /* 공백과 줄바꿈을 유지하면서 자동 줄바꿈 설정 */
    word-wrap: break-word; /* 긴 단어를 줄바꿈 하도록 설정 */
    overflow-x: hidden; /* 가로 스크롤바를 숨김 */
    overflow-y: auto; /* 세로 스크롤바를 자동으로 표시 */
    text-indent: calc(var(--create-font-size) * 0.8);
    margin-top: calc(var(--create-font-size) / 3);
    margin-bottom: calc(var(--create-font-size) / 3);
}
#boardCreateBoxExpression:focus, #boardCreateBoxName:focus{
    border: none;
    outline: none;   
}
#boardCreateBoxNameSpan{
    visibility: hidden;
    white-space: pre;
    display: inline-block;
}
#boardCreateBoxExpressionSpan{
    visibility: hidden;
    white-space: pre;
    display: inline-block;
}
#boardCreateBoxCover{
    width: 100lvw;
    height: 100lvh;
    position: fixed;
    top: 0;
    left:0;
    z-index: 10;
    background: rgba(0, 0, 0, 0.507);
}
#boardCreateBoxButton1{
    margin-top:30px;
}
#boardCreateBoxButton2{
    margin-top: 10px;
    margin-bottom: 10px;
}
#boardCreateBoxButton3{
    font-size: 1.1em;   
}
#boardCreateBoxMsg{
    z-index: 11;
    min-width: 300px;
    aspect-ratio: 3/2;
    position: fixed;
    top: 50%;
    left:50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(-50%, -50%);
    background: rgb(255, 255, 255);    
    clip-path: polygon(24% 0, 100% 0, 100% 50%, 100% 100%, 0 100%, 0 37%);    
    border-radius: 10px;
    font-size: 22px;
    font-family: 'mabinogi';
}
#foldPage{
    height: 50%;
    aspect-ratio: 1/1;
    background-image: url('@/assets/background/pageCover.png');
    background-size: auto 100%;
    position: absolute;
    top: 0px;
    left:0px;
    background-repeat: no-repeat;
    background-position : 0px 0px    

}
#backgroundBox{
    display: flex;
    flex-direction: row;
    align-items: center;    
}
.backgroundBoxInner{
    height: calc(var(--create-font-size) * 5);
    aspect-ratio: 2 / 3;
    border: 3px solid black;
    border-radius: calc(var(--create-font-size) * 1);
}
#backgroundBox>.backgroundBoxLeft{
    background-image: var(--backgroundCustomImageL);
    cursor: url("@/assets/cursor/def_left.cur"), default;
}
#backgroundBox>.backgroundBoxMiddle{
    height: calc(var(--create-font-size) * 7);
    background-image: var(--backgroundCustomImageM);
    margin-top: calc(var(--create-font-size) * 1.2);
    margin-left: calc(var(--create-font-size) * 1);
    margin-right: calc(var(--create-font-size) * 1);
    cursor: zoom-in;
}
#backgroundBox>.backgroundBoxMiddle:hover{
    scale: 1.3;
    transition: scale 0.5s;
}
#backgroundBox>.backgroundBoxRight{
    background-image: var(--backgroundCustomImageR);
    cursor: url("@/assets/cursor/def_right.cur"), default;
}

</style>