<template>
    <div id="popUpGlobalMsgBox" @click="clearPopUp" v-show="popUpOnTF">
        <div id="popUpGlobalMsgBackGround"></div>
        <div id="popUpGlobalMsg">
            <div id="popUpGlobalMsgfoldPage"></div>
            {{ massage }}
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
    data() {
        return {
            popUpOnTF: false,
            massage: ''
        }
    },
    computed: {
        ...mapState(['popUpMsg']),
        Msg() {
            return this.popUpMsg;
        }
    },
    watch: {
        Msg(newMsg) {
            if (newMsg != null) {
                // console.log('test중 성공')
                this.popUpOnTF = true;
                this.massage = newMsg.msg;
            } else {
                this.popUpOnTF = false;
                this.massage = ''
            }
        }
    },
    methods: {
        ...mapActions(['setPopUpMsg']),
        clearPopUp() {
            this.setPopUpMsg(null);
        }
    }

}
</script>

<style>
#popUpGlobalMsgBox{    
}
#popUpGlobalMsgBackGround {
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: black;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
}

#popUpGlobalMsg {
    z-index: 101;
    min-width: 300px;
    aspect-ratio: 3/2;
    position: fixed;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(-50%, -50%);
    background: rgb(255, 240, 212);
    clip-path: polygon(24% 0, 100% 0, 100% 50%, 100% 100%, 0 100%, 0 37%);
    border-radius: 10px;
    font-size: 22px;
    font-family: 'mabinogi';
}

#popUpGlobalMsgfoldPage {
    height: 50%;
    aspect-ratio: 1/1;
    background-image: url('@/assets/background/pageCover.png');
    background-size: auto 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    background-repeat: no-repeat;
    background-position: 0px 0px
}
</style>