<template>
  <nav>
    <div
      id="navMain"
      :class="navMainSignTF ? 'navLogOn' : 'navLogOff'"
      @mouseleave="hoverChildHome2"
    >
      <div id="navMainHome" @mouseenter="hoverChildHome">
        <span id="navMainHomeSpan" @click="showChildHome">
          <span>HO</span>
          <span>ME</span>
        </span>
        <router-link
          to="/sign-in"
          id="navMainHomeLog"
          v-show="navMainSignTF"
          v-if="!signedToken"
        >
          <span data-purpose="a">SIGN</span>
          <span data-purpose="a">IN</span>
        </router-link>
        <a
          href="#"
          @click.prevent="userInfoNav"
          id="navMainHomeLog"
          v-show="navMainSignTF"
          v-else-if="signedToken"
        >
          <span data-purpose="a">USER</span>
          <span data-purpose="a">INFO</span>
        </a>
      </div>
      
    </div>
    <p id="navMainLeft" v-if="lists.length > 6" @click="changeOrder(false)">
        <i class="fa-solid fa-angle-left"></i>
      </p>
      <p id="navMainRight" v-if="lists.length > 6" @click="changeOrder(true)">
        <i class="fa-solid fa-angle-right"></i>
      </p>
    <div
      v-for="(route, index) in lists"
      :key="route.themes"
      :class="updateClass(route)"
      class="navDiv"
      @mouseleave="navDivMouseLeave(index)"
    >
      <span
        class="navTitle"
        @click.self="
          showChildNavSize(route.items[0].path, index, route.Qsingle)
        "
        >{{ route.themes }}</span
      >
      <div v-if="!route.Qsingle" class="navArrow" @click="showChildNav(index)">
        ▼
      </div>
      <div v-else class="navArrow"></div>
      <div
        v-if="!route.Qsingle"
        class="navDivChilds"
        :class="route.childView ? 'navDivChildsView' : 'navDivChildsUnView'"
      >
        <childs v-for="el in route.items" :key="el.path" :elms="el" />
      </div>
    </div>
    <popUserInfo
      v-if="popUpUserInfo"
      @informClose="clearPopUpUserInfo"
      @informSignOut="signOutNav"
      :propToken="propToken"
    ></popUserInfo>
    <popSignOut v-if="popUpSignOut" @click="clearPopUpSignOut"> </popSignOut>
  </nav>
</template>

<script>
import nav from "@/services/naving.js";
import childs from "@/components/NavChild.vue";
import popSignOut from "@/components/popUpSignOut.vue";
import popUserInfo from "@/components/popUpUserInfo.vue";
import TokenService from "@/services/token/TokenService";
import { mapState, mapActions } from "vuex";

export default {
  components: {
    childs,
    popSignOut,
    popUserInfo,
  },
  props: {
    gcValue: {
      type: Object,
      default: null,
    },
  },
  computed: {
    routes() {
      return this.$store.state.routes;
    },
    ...mapState(["token"]),
    signedToken() {
      return this.token;
      //return this.$store.state.token; 위랑 같음.
    }, //불러온 token을 signedToken에 삽입! 여기서 잠깐 왜 signedToken을 data에서 선언하지 않았나요? 그야 지금 computed에서 만들어지기 때문임. 여기서 만약에 data에서 선언하면 null로 덮어져버린다!
  },
  watch: {
    signedToken(newToken) {
      if (newToken === null) {
        this.refreshTokenVuex();
      } //signedToken을 상태 감시.
    },
  },
  data() {
    return {
      propToken: null,
      popUpUserInfo: false,
      popUpSignOut: false,
      lists: [],
      navMainSignTF: false,
      navMainSignTF2: false,
      navMainLong: false,
      navMainNums: 0,
      windowWidth: window.innerWidth,
    };
  },
  mounted() {
    if (this.signedToken === null) {
      this.refreshTokenVuex();
    }
    window.addEventListener("resize", this.updateWidth);
    const windowWidth = window.innerWidth;
    if (windowWidth >= 1200) {
      this.navMainNums = 45;
    } else if (windowWidth < 1200 && windowWidth >= 700) {
      this.navMainNums = 35;
    } else {
      this.navMainNums = 30;
    }
    this.setDef();
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.updateWidth);
  },
  methods: {
    ...mapActions(["refreshTokenVuex", "signOut"]),
    setDef() {
      let a = nav.getNavBar(this.$store.state.routes, this.signedToken);
      this.lists = [...a];
    },
    handleNavClick(event) {
      console.log(event.target.tagName);
      if (
        event.target.tagName === "A" ||
        event.target.getAttribute("data-purpose") == "a"
      ) {
        this.defaultingNav();
      }
      if (
        event.target.tagName != "A" &&
        (event.target.getAttribute("data-purpose") != "a" ||
          event.target.getAttribute("data-purpose") == null) &&
        this.windowWidth < 700 &&
        this.navMainSignTF
      ) {
        if (!this.navMainSignTF2) {
          this.navMainSignTF2 = true;
        } else {
          this.navMainSignTF2 = false;
          this.defaultingNav();
        }
      }
    },
    updateWidth() {
      let a = this.windowWidth;
      console.log("updateWidth", this.windowWidth, "new", window.innerWidth);
      if (a != window.innerWidth) {
        const windowWidth = window.innerWidth;
        if (windowWidth >= 1200) {
          this.navMainNums = 45;
        } else if (windowWidth < 1200 && windowWidth >= 700) {
          this.navMainNums = 35;
        } else {
          this.navMainNums = 30;
        }
        this.windowWidth = window.innerWidth;
        this.defaultingNav();
      }
    },
    changeOrder(direction) {
      let d = [...this.lists];
      let a = [];
      d.forEach((el) => {
        let c = { ...el };
        console.log("c", c);
        a.push(c);
      });
      let e = new Array(6);
      a.forEach((el) => {
        switch (el.Qpos) {
          case "left3":
            e.splice(0, 1, el);
            break;
          case "left2":
            e.splice(1, 1, el);
            break;
          case "left1":
            e.splice(2, 1, el);
            break;
          case "right1":
            e.splice(3, 1, el);
            break;
          case "right2":
            e.splice(4, 1, el);
            break;
          case "right3":
            e.splice(5, 1, el);
            break;
          default:
            e.push(el);
            break;
        }
      });
      console.log("e",e)
      if (direction) {
        e[0].Qpos = "";
        e[0].Qview = false;
        let z = e[0];
        e.splice(0, 1);
        e.push(z);
        e[0].Qpos = "left3";
        e[1].Qpos = "left2";
        e[2].Qpos = "left1";
        e[3].Qpos = "right1";
        e[4].Qpos = "right2";
        e[5].Qpos = "right3";
        e[5].Qview = true;
      } else if (!direction) {
        e[5].Qpos = "";
        e[5].Qview = false;
        let z = e[e.length - 1];
        e.splice(e.length - 1, 1);
        e.splice(0, 0, z);
        e[0].Qpos = "left3";
        e[1].Qpos = "left2";
        e[2].Qpos = "left1";
        e[3].Qpos = "right1";
        e[4].Qpos = "right2";
        e[5].Qpos = "right3";
        e[0].Qview = true;
      }
      this.lists = [...e];
      this.giveAnimation(direction);
    },
    giveAnimation(direction) {
      let viewingB = document.querySelectorAll(".unviewing");
      for (let i = 0; i < viewingB.length; i++) {
        viewingB[i].style.transition =
          "transform 0.3s cubic-bezier(0, 0, 0.15, 1.08)";
        if (direction) {
          viewingB[i].style.transform = "translateX(30%)";
        } else {
          viewingB[i].style.transform = "translateX(-30%)";
        }
      }
      setTimeout((e) => {
        for (let i = 0; i < viewingB.length; i++) {
          viewingB[i].style.transform = "translateX(0%)";
        }
      }, 300);
    },
    updateClass(el) {
      return {
        viewing: el.Qview,
        unviewing: !el.Qview,
        [el.Qpos]: true,
      };
    },
    defaultingNav() {
      const windowWidth = window.innerWidth;
      if (windowWidth >= 1200) {
        this.navMainNums = 45;
      } else if (windowWidth < 1200 && windowWidth >= 700) {
        this.navMainNums = 35;
      } else {
        this.navMainNums = 30;
      }
      const navMainHome = document.querySelector("#navMainHome");
      const navMainHomeSpan = document.querySelector("#navMainHomeSpan");
      navMainHome.style.height = `100%`;
      navMainHomeSpan.style.height = "100%";
      this.navMainSignTF = false;
      this.navMainSignTF2 = false;
      this.navMainLong = false;
      for (let item of this.lists) {
        item.childView = false;
      }
    },
    showChildNav(index) {
      let a = this.lists[index].childView;
      for (let item of this.lists) {
        item.childView = false;
      }
      this.lists[index].childView = !a;
    },
    showChildNavSize(paths, index, single) {
      const windowWidth = window.innerWidth;
      const path = paths;
      if (windowWidth <= 699) {
        if (single) {
          this.defaultingNav();
          this.$router.push({ path });
        } else {
          this.showChildNav(index);
        }
      } else {
        this.defaultingNav();
        this.$router.push({ path });
      }
    },
    showChildHome() {
      const windowWidth = window.innerWidth;
      if (windowWidth <= 699 && !this.navMainSignTF) {
        this.navMainSignTF = true;
        const navMain = document.querySelector("#navMain");
        navMain.classList.replace("navLogOff", "navLogOn"); //짜증나서 그냥 때려박음 아니 왜 자꾸 router 변경하고 나서 첫 클릭할 때 재깍 안 바뀌냐고 ㅡㅡ
        const navMainHome = document.querySelector("#navMainHome");
        const navMainHomeSpan = document.querySelector("#navMainHomeSpan");
        navMainHome.style.height = `200%`;
        navMainHomeSpan.style.height = "50%";
      } else {
        this.defaultingNav();
        this.$router.push({ path: "/" });
      }
    },
    hoverChildHome() {
      const windowWidth = window.innerWidth;
      if (windowWidth > 699 && !this.navMainLong) {
        this.navMainSignTF = true;
        this.navMainLong = true;
        const navMainHome = document.querySelector("#navMainHome");
        const navMainHomeSpan = document.querySelector("#navMainHomeSpan");
        navMainHome.style.height = `200%`;
        navMainHomeSpan.style.height = "50%";
      }
    },
    hoverChildHome2() {
      const windowWidth = window.innerWidth;
      if (windowWidth > 699) {
        const navMainHome = document.querySelector("#navMainHome");
        const navMainHomeSpan = document.querySelector("#navMainHomeSpan");
        navMainHome.style.height = `100%`;
        navMainHomeSpan.style.height = "100%";
        this.navMainSignTF = false;
        this.navMainLong = false;
      }
    },
    userInfoNav() {
      if (TokenService.getToken() != null) {
        this.propToken = TokenService.decodeToken().data;
        this.popUpUserInfo = true;
        this.defaultingNav();
      } else {
        this.propToken = null;
        this.defaultingNav();
      }
    },
    signOutNav() {
      this.popUpUserInfo = false;
      if (TokenService.removeToken().status == 202) {
        this.signOut();
        this.popUpSignOut = true;
        this.defaultingNav();
        this.$router.push({ path: "/" });
        this.setDef();
      }
    },
    clearPopUpSignOut() {
      this.popUpSignOut = false;
    },
    clearPopUpUserInfo() {
      this.popUpUserInfo = false;
    },
    navDivMouseLeave(index) {
      const windowWidth = window.innerWidth;
      let a = this.lists[index].childView;
      if (windowWidth > 699 && a) {
        this.lists[index].childView = false;
      }
    },
  },
};
</script>

<style >
:root{
  --navDivHeight : 45px;
}
nav {
  padding-top: 1%;
  max-width: 100%;
  min-width: 300px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: start;
  position: relative;
  user-select: none;
  /* 드래그 방지 */
  z-index: 2;
}

nav i,
nav a,
nav span {
  color: black;
}

nav .navDiv {
  background: #ffffff;
  display: inline-flex;
  position: relative;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  height: var(--navDivHeight);
  top: 10%;
  font-family: "mabinogi", "mabinogi";
  border:5px solid rgb(228, 107, 59);
  border-radius: 3px;
  margin-left: 3px;
}

nav .navDiv > .navTitle {
  position: absolute;
  display: inline-block;
  width: 80%;
  max-height: 100%;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.3;
  text-align: center;
}

nav .navDiv > .navArrow {
  position: absolute;
  right: 3%;
  font-size: 100%;
  width: 10%;
  max-height: 100%;
  cursor: zoom-in;
}

nav #navMain {
  position: relative;
  transition: box-shadow 1s;
  width: calc(100% / 7 / 3);
  height: var(--navDivHeight);
  padding-top: 0.5%;
  padding-bottom: 0.5%;
  display: block;
  box-sizing: content-box;
}

nav #navMainHome {
  box-shadow: -2px -2px 0px 2px black, 2px -2px 0px 2px black,
    -2px 2px 0px 2px black, 2px 2px 0px 2px black;
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  transform-origin: top;
  transition: height 0.5s;
  background-color: #f7f9f8;
}

nav #navMainHome #navMainHomeSpan {
  width: 100%;
  height: 100%;
  display: inline-flex;
  cursor: pointer;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding-bottom: 10%;
}

nav #navMainHome #navMainHomeSpan > span {
  display: inline-block;
  width: 100%;
  height: 50%;
  font-family: "amaticreg";
  text-align: center;
  font-size: 40px;
  font-weight: 900;
  transform: scaleY(0.7);
}

nav #navMainHome #navMainHomeLog {
  width: 100%;
  height: 50%;
  display: inline-flex;
  cursor: pointer;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding-bottom: 10%;
}

nav #navMainHome #navMainHomeLog > span {
  display: inline-block;
  width: 100%;
  height: 50%;
  font-family: "amaticreg";
  text-align: center;
  font-size: 30px;
  font-weight: 900;
  transform: scaleY(0.7);
}

nav #navMainLeft {
  margin-left: 6px;    
  margin-right:2px;
  font-size: 100%;
  height: var(--navDivHeight);
  line-height: var(--navDivHeight);
  width:1em;
  background: white;
  text-align: center;
  border-radius: 0.2em;
  cursor: url("@/assets/cursor/def_left.cur"), default;
}

nav #navMainRight {
  line-height: var(--navDivHeight);
  margin-left: 2px;    
  margin-right:2px;
  font-size: 100%;
  height: var(--navDivHeight);
  width:1em;
  border-radius: 0.2em;
  background: white;
  text-align: center;
  cursor: url("@/assets/cursor/def_right.cur"), default;
}

nav .viewing {
  display: inline-flex;
  width: calc(95% / 7);  
}
nav .viewing::before {
  content: "";
  width: calc(100% - 14px);
  height: calc(100% + 10px);
  position: absolute;
  background: inherit;
  
}
/*
nav .viewing.left1 {
  left: calc(100% / 7 * 2);
  
}

nav .viewing.left2 {
  left: calc(100% / 7);
  
}

nav .viewing.left3 {
  left: 0%;
}

nav .viewing.right1 {
  right: calc(100% / 7 * 2);
}

nav .viewing.right2 {
  right: calc(100% / 7);
  
}

nav .viewing.right3 {
  right: 0%;
  
}
*/
@media (min-width: 1200px) {
  nav .navDiv {
    font-size: 110%;
  }

  nav #navMain {
    height: 45px;
  }

  nav #navMainLeft,
  nav #navMainRight {
    top: 35%;
  }
}

@media (min-width: 700px) and (max-width: 1199px) {
  nav #navMain {
    height: 35px;
  }

  nav #navMainHome #navMainHomeSpan > span {
    font-size: 30px;
  }

  nav #navMainHome #navMainHomeLog > span {
    font-size: 20px;
  }

  nav #navMainLeft,
  nav #navMainRight {
    height: 35px;
    line-height: 35px;
  }
  nav .navDiv{
    height: 35px;
  }
}

@media (max-width: 699px) {
  nav .navDiv {
    font-size: 90%;
    height: 30px;
  }

  nav .navDiv > .navArrow {
    display: none;
  }

  nav #navMainLeft {
    height: 30px;
    line-height: 30px;
  }

  nav #navMainRight {
    height: 30px;
    line-height: 30px;
  }

  nav #navMain {
    height: 30px;
    width: calc(100% / 5 / 3);
  }

  nav #navMainHome {
    box-shadow: -1px -1px 0px 1px black, 1px -1px 0px 1px black,
      -1px 1px 0px 1px black, 1px 1px 0px 1px black;
  }

  nav #navMainHome #navMainHomeSpan > span {
    font-size: 26px;
  }

  nav #navMainHome #navMainHomeLog > span {
    font-size: 22px;
  }

  nav #navMain.navLogOn #navMainHome {
    height: 200%;
  }

  nav #navMain.navLogOn #navMainHome > #navMainHomeSpan {
    height: 50%;
  }

  nav .viewing {
    display: inline-flex;
    width: calc(98% / 5);
  }

  /*nav .viewing.left1 {
    left: calc(100% / 5);
  }

  nav .viewing.left2 {
    left: calc(0%);
  }

  nav .viewing.left3 {
    display: none;
  }

  nav .viewing.right1 {
    right: calc(100% / 5);
  }

  nav .viewing.right2 {
    right: calc(0%);
  }

  nav .viewing.right3 {
    display: none;
  }*/
}

nav .unviewing {
  display: none;
}

nav .navDivChilds {
  position: absolute;
  width: 100%;
  background: aliceblue;
  top: 100%;
  left: 0%;
  display: none;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

nav .navDivChilds.navDivChildsUnView {
  display: none;
}

nav .navDivChilds.navDivChildsView {
  display: flex;
}

nav .navDivChilds > a {
  display: inline-block;
  color: red;
  padding-top: 5%;
  padding-bottom: 5%;
}
</style>
