<template>
    <form id="articleAdjustBox" @submit.prevent="handleSubmit">
        <div id="articleAdjustBoxRow1">
            <span @click="focus">제목</span>
            <input type="text" name="articleAdjustBoxTitle" id="articleAdjustBoxTitle" v-model="articleAdjustBoxTitle" maxlength="100"/>
        </div>
        <div id="articleAdjustBoxRow2">
            <span @click="focus">내용</span>
            <div id="articleAdjustBoxContent" contenteditable="true">
                <p><br></p>
            </div>
        </div>
        <div id="articleAdjustBoxRow3">
            <button type="button" id="articleAdjustBoxButton1" @click="callOut">나가기</button>
            <button type="button" id="articleAdjustBoxButton2" @click="callDef">초기화</button>
            <button type="button" id="articleAdjustBoxButton3" @click="callSave">저장</button>
        </div>
    </form>
</template>

<script>
import ArticleService from '@/java/service/ArticleService';

export default {
    emits : [
        'informOut'
    ],
    props :{
        boardInfo:{
            type:Object,
            required : true,            
        },
        articleInfo:{
            type:Object,
            required : true,            
        },
        tokenData:{
            type:Object,
            required : true,            
        },
    },
    data(){
        return{            
            articleAdjustBoxTitle : this.articleInfo.articleTitle,
            test:this.articleInfo.articleContent,
        }
    },
    mounted(){        
        this.callDef();
    },
    methods : {
        async callSave(){
            const articleAdjustBoxContent =document.getElementById('articleAdjustBoxContent')
            if(this.articleAdjustBoxTitle==''){
                document.getElementById('articleAdjustBoxTitle').focus();
                return;
            }
            if(this.tokenData.userNo==null||this.tokenData.userNo==0||this.boardInfo==null||this.boardInfo.boardNo==0){
                this.callOut()
                return;
            }            
            this.test = articleAdjustBoxContent.innerHTML
            let customObject = {
                articleTitle : this.articleAdjustBoxTitle,
                articleContent : this.test,
                articleNo : this.articleInfo.articleNo,
            }            
            let res = await ArticleService.reWriteArticle(customObject);
            try {
                //글 읽는 곳으로 이동시킬 것                
                let a = String(res.data.board.boardNo).split('').map(digit =>  String.fromCharCode((parseInt(digit) % 26) + 97)).join('');
                let b =String(res.data.articleNo).split('').map(digit =>  String.fromCharCode((parseInt(digit) % 26) + 97)).join('');
                let c = a + "#" + b     
                console.log(c)           
                this.$emit('informOut')
            } catch (error) {
                
            }
            
        },
        callDef(){
            const articleAdjustBoxContent =document.getElementById('articleAdjustBoxContent')
            articleAdjustBoxContent.innerHTML = this.test
            this.articleAdjustBoxTitle=this.articleInfo.articleTitle            
        },
        callOut(){
            this.$emit('informOut')
        },
        focus(e){
            e.target.nextSibling.focus();
        },
    }
}
</script>

<style>
    :root{
        --articleAdjust-font-size:17px;
    }
    #articleAdjustBox{
        width: 100%;
        min-height: var(--calculatedRouterDivHeight);
        align-self: stretch;
        display: flex;
        flex-direction: column;        
        position: relative;
        flex-grow: 1;
        background: rgba(255, 211, 169, 0.774);
    }
    #articleAdjustBox *{
        font-family: 'ridi';
    }
    #articleAdjustBox>#articleAdjustBoxRow1{
        flex-grow: 0;
        display: flex;
        flex-direction: column;
        align-items: start;
        margin-left: 10%;
    }
    #articleAdjustBox>#articleAdjustBoxRow1>span:first-of-type{
        font-size: calc(var(--articleAdjust-font-size) * 1.5);
        text-indent: calc(var(--articleAdjust-font-size) * 1.5);        
        display: inline-block;
        min-width: 90%;
        padding-top: calc(var(--articleAdjust-font-size) * 0.5);
        padding-bottom: calc(var(--articleAdjust-font-size) * 0.5);
        user-select: none;
        cursor: pointer;
    }
    #articleAdjustBox>#articleAdjustBoxRow1>input{
        font-size: calc(var(--articleAdjust-font-size) * 0.85);
        text-indent: var(--articleAdjust-font-size);
        flex-grow: 0;
        min-width: 90%;
        min-height: calc(2em);
        outline: none;
        border: 1px solid black;
        background-color: rgba(255, 255, 255, 0.932);
        border-radius: 8px;
    }
    #articleAdjustBox>#articleAdjustBoxRow2{        
        margin-left: 10%;        
    }
    #articleAdjustBox>#articleAdjustBoxRow2>#articleAdjustBoxContent{
        border-radius: 8px;
        font-size: calc(var(--articleAdjust-font-size) * 0.85);
        outline: none;
        border: 1px solid black;
        max-width: 90%;
        min-height: calc(10em);
        background-color: rgba(255, 255, 255, 0.945);
        font-size: var(--articleAdjust-font-size*0.85);
        padding: calc(var(--articleAdjust-font-size) * 0.5);        
        margin-bottom: 5em;
    }
    #articleAdjustBox>#articleAdjustBoxRow2>span:first-of-type{
        font-size: calc(var(--articleAdjust-font-size) * 1.5);
        text-indent: calc(var(--articleAdjust-font-size) * 1.5);
        display: inline-block;
        min-width: 90%;
        padding-top: calc(var(--articleAdjust-font-size) * 0.5);
        padding-bottom: calc(var(--articleAdjust-font-size) * 0.5);        
        user-select: none;
        cursor: pointer;
    }
    #articleAdjustBox>#articleAdjustBoxRow3{
        display: flex;
        flex-direction: column;
        position: absolute;
        bottom:10px;
        right:10px;
        align-items: flex-end;
    }   
    #articleAdjustBox>#articleAdjustBoxRow3>button{
        user-select: none;
        cursor: pointer;
    }
    #articleAdjustBox>#articleAdjustBoxRow3>button:hover{
        text-shadow: 1px 1px black;
        color: yellow;
    }
    #articleAdjustBox>#articleAdjustBoxRow3>#articleAdjustBoxButton1{

    }
    #articleAdjustBox>#articleAdjustBoxRow3>#articleAdjustBoxButton2{
        margin-top:calc(var(--articleAdjust-font-size) * 0.2);;
        margin-bottom:calc(var(--articleAdjust-font-size) * 0.2);;
    }
    #articleAdjustBox>#articleAdjustBoxRow3>#articleAdjustBoxButton3{

    }

</style>