class tailoring {
    breakSt(elm) {
        return (elm.split(" ")).map(z => z.split(""));
    }
    longestArray(elm) {
        let q = {
            Qsingle: true,
            items: []
        }
        let a = elm.map(z => z.length);
        let b = Math.max(...a);
        let c = a.map((length, index) => (length === b ? index : -1)).filter(index => index !== -1);
        q.items.push(c);
        console.log(c)
        if (c.length == 1) {
            let d = [...a];
            console.log(d)
            d.splice(c[0], 1);
            let e = Math.max(...d);
            let f = a.map((length, index) => (length === e ? index : -1)).filter(index => index !== -1);
            q.Qsingle = false;
            q.items.push(f)
            return q;
            //첫 친구 제일 긴 친구 하나, 두번째 친구는 두번째로 긴 친구들
        } else {

            return q;
            //여러 친구들 중 다 긺
        }
    }
    partArray(elm) {
        let a = [...elm];
        let b = 0;
        for (let z = 0; z < a.length; z++) {
            if (a[z].Qbox) {
                switch (b) {
                    case 0:
                        if (z == a.length - 2) {
                            b++;
                            a[z].lines = b;
                        } else {
                            a[z].lines = b;
                            b++;
                        }
                        break;
                    case 1:
                        b++;
                        a[z].lines = b;
                        break;
                }
            } else {
                a[z].lines = b;
            }



        }
        return a;
    }
}

export default new tailoring();
