import http from '@/java/http-common';
import TokenService from '@/services/token/TokenService';
import DataService from '@/java/service/DataService';

class BoardService{
    async saveNewBoard(data){
        try {
            let result = await http.post(`/board/addNew`,data)
            if(result.status == 201){//HttpStatus.CREATED
                return result;
            }else{
                return { status: 400, message: 'An error occurred during sign-on.BAD REQUEST' };
            }
        } catch (e) {
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }

    async readAllBoard(data){   
          
        try{
            let result = await http.post('/board/readAll',data);
            console.log(result)
            return result;
        }catch(e){
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }

    async deleteBoard(data, no){
        try{
            let newData = data;
            newData.boardNo = no;
            let result = await http.post('/board/delete',newData);
            return result;
        }catch(e){
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }
    async liveBoard(data, no){
        try{
            let newData = data;
            newData.boardNo = no;
            let result = await http.post('/board/live',newData);
            return result;
        }catch(e){
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }
}
export default new BoardService();