<template >
  <div @click="handleGlobalClick" id="appdiv">
    <Pop></Pop>
    <Nav ref="navComponent"/>
    <article id="routerDiv"><router-view/></article>
    <Fot></Fot>
  </div>
</template>
<script>
  import { ref } from 'vue';
  import { mapActions } from 'vuex';
  import Fot from '@/components/Footer.vue'
  import Nav from '@/components/Nav.vue'
  import Pop from '@/components/popUpMsg.vue'
  import TokenService from '@/services/token/TokenService.js';
  export default {
    name : 'App',
    components : {
      Nav, Pop, Fot
    },
    data(){
      return{
        gcValue : null,
        appDom : ref({
          domNavWidth : null,
          domNavHeight : null,
          totalHeight : null,
          calculatedRouterDivMargin : null,
          calculatedRouterDivHeight : null,
        }),
        popUpMsg : null
      }
    },
    methods : {
      ...mapActions(['callDom']),
      updateSize(){
      
      const domNav = document.querySelector('nav')
      const totalHeight = window.innerHeight;
      const domNavHeight = domNav.getBoundingClientRect().height*1.14;
      document.documentElement.style.setProperty('--calculatedRouterDivMargin',`calc(${domNavHeight*0.12}px)`);
      document.documentElement.style.setProperty('--calculatedRouterDivHeight',`calc(${totalHeight}px - ${domNavHeight}px)`);
      this.appDom.domNavWidth = domNav.getBoundingClientRect().width;
      this.appDom.domNavHeight =domNavHeight;
      this.appDom.totalHeight = totalHeight;
      this.appDom.calculatedRouterDivMargin = domNavHeight * 0.12;
      this.appDom.calculatedRouterDivHeight = totalHeight - domNavHeight;
      console.log(this.appDom)
      this.callDom(this.appDom)
      }
    },
    mounted(){
      //this.updateSize = debounce(this.updateSize, 200);
      window.addEventListener('resize', this.updateSize);
      this.updateSize();
    },
    beforeUnmount() {
    window.removeEventListener('resize', this.updateSize);
    },
    beforeDestroy() {
    window.removeEventListener('resize', this.updateSize);
    } ,
    created(){
      //this.initializeApp();
    },  
    setup(){
      const navComponent = ref(null);
      const handleGlobalClick = (event)=>{        
        if(navComponent.value){
          navComponent.value.handleNavClick(event)
        }
      };

      return {
        navComponent,
        handleGlobalClick
      }
    }
  }
</script>

<style>
#appdiv{
  position: relative;
}
  #routerDiv{
    width: 100%;
    min-height: var(--calculatedRouterDivHeight);
    margin-top : var(--calculatedRouterDivMargin);
    position: relative;
    background-color: rgb(10, 10, 10);
  }
  body{
    background-color: rgb(3,3,3);
  }
</style>
