import http from '@/java/http-common';

class ArticleService{
    async readArticles(data){
        console.log(data,"data")
        try {
            let result = await http.post('/article/readAll',data)
            console.log(result,"result")
            if(result.status === 204){
                return {status:204,data:result.data}
            }else if(result.status === 200){
                return {status:200,data:result.data}
            }else{
                return {status:500,data:null}
            }
        } catch (error) {
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }
    async readArticle(no){
        try {
            let result = await http.get(`/article/read-${no}`);
            if(result.status==200){
                return {status : 200, data : result.data}
            }else{
                return {status : 500, data : null}
            }
        } catch (error) {
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }
    async dumpArticles(no, list){
        try {
            let result = await http.post(`/article/dump-${no}`,list)
            if(result.status === 204){
                return {status:204,data:result.data}
            }else if(result.status === 200){
                let a = result.data.length;
                result.data.forEach(item=>{
                    item.board.articleCount = a
                })
                localStorage.setItem('boardInfo',JSON.stringify(result.data[0].board))
                return {status:200,data:result.data}
            }else{
                return {status:500,data:null}
            }
        } catch (error) {
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }
    async writeArticle(data){
        try {
            let result = await http.post(`/article/write`,data);
            if(result.status == 200){
                return {status:200,data:result.data}
            }else{
                return {status:500,data:null}
            }
        } catch (error) {
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }
    async reWriteArticle(data){
        try {
            console.log(data)
            let result = await http.post(`/article/rewrite`,data);
            
                return {status:200,data:result.data}            
                
        } catch (error) {
            return { status: 500, message: 'An error occurred during sign-in. INTERNAL_SERVER_ERROR' };
        }
    }
}
export default new ArticleService();