class Card{
     constructor(suit, value, x, y, fold = false, size = { w: 800, h: 600 }) {
        this.suit = suit;
        this.value = value;
        this.x = (x / 800) * size.w;
        this.y = (y / 800) * size.h;
        this.fold = fold;
        this.updateSize(size);
    }

    updateSize(size) {
        this.width = (63 / 800) * size.w;
        this.height = (88 / 600) * size.h;
        let originSuitPx = (30 / 800) * size.w;
        let originNumPx = (45 / 800) * size.w;
        let originSuitX = (5 / 800) * size.w;
        let originSuitY = (7 / 600) * size.h + (20 / 600) * size.h;
        let originNumX = (19 / 800) * size.w;
        let originNumY = (35 / 600) * size.h + (30 / 600) * size.h;
        this.suit <= 1 ? this.suits = ["red", `${originSuitPx}px 'pokers'`, originSuitX, originSuitY] : this.suits = ["black", `${originSuitPx}px 'pokers'`, originSuitX, originSuitY];
        this.suit <= 1 ? this.nums = ["red", `${originNumPx}px 'poker'`, originNumX, originNumY] : this.nums = ["black", `${originNumPx}px 'poker'`, originNumX, originNumY];
    }

    draw(context) {
        let suit = suits[this.suit];
        let num = values[this.value];
        context.fillStyle = "rgb(0 0 0 / 50%)";
        context.fillRect(this.x + 10, this.y + 8, this.width, this.height);
        console.log(this.x + 10, this.y + 8, this.width, this.height)
        context.strokeStyle = "black";
        context.lineWidth = 3;
        context.fillStyle = "white";
        context.fillRect(this.x, this.y, this.width, this.height);
        context.strokeRect(this.x, this.y, this.width, this.height);
        if (!this.fold) {
            context.fillStyle = this.suits[0];
            context.font = this.suits[1];
            context.fillText(suit, this.x + this.suits[2], this.y + this.suits[3]);

            context.fillStyle = this.nums[0];
            context.font = this.nums[1];
            context.fillText(num, this.x + this.nums[2], this.y + this.nums[3]);
        } else {
            context.fillStyle = "green";
            context.fillRect(this.x+3, this.y+3, this.width - 6, this.height - 6);
        }
    }
}

class Deck{
    constructor(decknum = 1){
        this.suits = Array.from({length:4},(_, i)=>i);//하트 다이아 스페이드 클로버
        this.values = Array.from({length:13},(_, i)=>i+1);//0호출하면 1임. 그래서 카드의 1을 호출하게 되어 a(1) 이 나옴;
        this.deck = [];
        this.deckdiscard = [];
        this.decknum = decknum;

        this.initializeDeck();    
    }
    initializeDeck(){
        for(let n=0; n<this.decknum; n++){
            for(let suit of this.suits){
                for(let value of this.values){
                    this.deck.push(new Card(suit, value, 0, 0))
                }
            }
        }   
        // this.shuffleDeck();
    };
    drawCard(){        
        if(this.deck.length ===0){
            return null; //no more card
        }
        let card = this.deck.pop();
        this.deckdiscard.push(card);
        return card;
    }
    resetDeck(){
        this.deck=[...this.deck, ...this.deckdiscard];
        this.deckdiscard = [];
        this.shuffleDeck();
    }
    shuffleDeck(){        
        for(let i = this.deck.length - 1 ; i>0; i--){
            let j = Math.floor(Math.random() * (i + 1));
            [this.deck[i], this.deck[j]] = [this.deck[j],this.deck[i]]
        }
        
    }
    
    drawSpecificCard(suit, value){
        let index = this.deck.findIndex(card => card.suit === suit && card.value === value);
        if(index != -1){
            let [card] = this.deck.splice(index, 1);
            this.deckdiscard.push(card);
            return card;
        }
        return null;
    }
}

export default game21;
