//한 번이라도 중복되면 여기에 넣어서 사용합시다.
import mathing from '@/services/mathing'

class defaulting{
    setFocus(selectedQuery){
        let selectedBox = selectedQuery;
        let selectedBoxFirstChild = selectedBox.firstChild;
        let range = document.createRange(); // Range 객체 생성-텍스트, 엘리먼트 등의 선택 영역.
        let sel = window.getSelection();//현재 문ㅅ의 선택 객체 가져오기. 클라이언트가 선택한 텍스트 또는 요소
        range.setStart(selectedBoxFirstChild, 0)//Node 호출 및, Range의 시작 지점을 설정. 커서를 놓을 요소 및 지점
        range.collapse(true)//Range 축소, 단일 지점으로 축소함.
        sel.removeAllRanges();//현재 선택한 영역 모두 제거
        sel.addRange(range)//새로운 range를 선택 영역에 추가
    }
    setFocusSibling(selectedQuery){
        let selectedBox = selectedQuery;
        let selectedBoxFirstChild = selectedBox;
        console.log(selectedBoxFirstChild)
        let range = document.createRange(); // Range 객체 생성-텍스트, 엘리먼트 등의 선택 영역.
        let sel = window.getSelection();//현재 문ㅅ의 선택 객체 가져오기. 클라이언트가 선택한 텍스트 또는 요소
        range.setStart(selectedBoxFirstChild, 0)//Node 호출 및, Range의 시작 지점을 설정. 커서를 놓을 요소 및 지점
        range.collapse(true)//Range 축소, 단일 지점으로 축소함.
        sel.removeAllRanges();//현재 선택한 영역 모두 제거
        sel.addRange(range)//새로운 range를 선택 영역에 추가
    }
    setColor(colorList){
        let rndInt = mathing.rndInt(0,colorList.length-1);        
        return colorList[rndInt]
    }
    setColorLighten(color, percent) {
        let num = parseInt(color.slice(1), 16),
        amt = Math.round(2.55 * percent),
        R = (num >> 16) + amt,
        G = (num >> 8 & 0x00FF) + amt,
        B = (num & 0x0000FF) + amt;
  
    
        R = R < 0 ? 0 : R > 255 ? 255 : R;
        G = G < 0 ? 0 : G > 255 ? 255 : G;
        B = B < 0 ? 0 : B > 255 ? 255 : B;
  
        return `#${(0x1000000 + R * 0x10000 + G * 0x100 + B).toString(16).slice(1).toUpperCase()}`;
    }
    mountData(bowlData, newData){
        let newResult = JSON.parse(JSON.stringify(bowlData));
        for (let key in newResult){
            if(Object.hasOwn(newData, key)){
                newResult[key] = newData[key]
            }
        }
        return newResult;
    }
    
}
export default new defaulting;