<template>
    <form id="articleCreateBox" @submit.prevent="handleSubmit">
        <div id="articleCreateBoxRow1">
            <span @click="focus">제목</span>
            <input type="text" name="articleCreateBoxTitle" id="articleCreateBoxTitle" v-model="articleCreateBoxTitle" maxlength="100"/>
        </div>
        <div id="articleCreateBoxRow2">
            <span @click="focus">내용</span>
            <div id="articleCreateBoxContent" contenteditable="true">
                <p><br></p>
            </div>
        </div>
        <div id="articleCreateBoxRow3">
            <button type="button" id="articleCreateBoxButton1" @click="callOut">나가기</button>
            <button type="button" id="articleCreateBoxButton2" @click="callDef">초기화</button>
            <button type="button" id="articleCreateBoxButton3" @click="callSave">저장</button>
        </div>
    </form>
</template>

<script>
import ArticleService from '@/java/service/ArticleService';

export default {
    emits : [
        'informOut'
    ],
    props :{
        boardInfo:{
            type:Object,
            required : true,            
        },
        tokenData:{
            type:Object,
            required : true,            
        },
    },
    data(){
        return{
            articleCreateBoxTitle : '',
            test:''
        }
    },
    methods : {
        async callSave(){
            const articleCreateBoxContent =document.getElementById('articleCreateBoxContent')
            if(this.articleCreateBoxTitle==''){
                document.getElementById('articleCreateBoxTitle').focus();
                return;
            }
            if(this.tokenData.userNo==null||this.tokenData.userNo==0||this.boardInfo==null||this.boardInfo.boardNo==0){
                this.callOut()
                return;
            }            
            this.test = articleCreateBoxContent.innerHTML
            let customObject = {
                articleTitle : this.articleCreateBoxTitle,
                articleContent : this.test,
                user : this.tokenData,
                board : this.boardInfo,                
            }            
            let res = await ArticleService.writeArticle(customObject);
            try {
                //글 읽는 곳으로 이동시킬 것
                console.log(res)
                let a = String(res.data.board.boardNo).split('').map(digit =>  String.fromCharCode((parseInt(digit) % 26) + 97)).join('');
                let b =String(res.data.articleNo).split('').map(digit =>  String.fromCharCode((parseInt(digit) % 26) + 97)).join('');
                let c = a + "#" + b     
                console.log(c)           
                this.$router.push({name : 'article', params : {nums : c}})
            } catch (error) {
                
            }
            
        },
        callDef(){
            const articleCreateBoxContent =document.getElementById('articleCreateBoxContent')
            articleCreateBoxContent.innerHTML = '<p><br></p>'
            this.articleCreateBoxTitle=''
            console.log(this.tokenData)
        },
        callOut(){
            this.$emit('informOut')
        },
        focus(e){
            e.target.nextSibling.focus();
        },
    }
}
</script>

<style>
    :root{
        --articleCreate-font-size:17px;
    }
    #articleCreateBox{
        align-self: stretch;
        display: flex;
        flex-direction: column;        
        position: relative;
        flex-grow: 1;
        
    }
    #articleCreateBox *{
        font-family: 'ridi';
    }
    #articleCreateBox>#articleCreateBoxRow1{
        flex-grow: 0;
        display: flex;
        flex-direction: column;
        align-items: start;
        margin-left: 10%;
    }
    #articleCreateBox>#articleCreateBoxRow1>span:first-of-type{
        font-size: calc(var(--articleCreate-font-size) * 1.5);
        text-indent: calc(var(--articleCreate-font-size) * 1.5);
        background-color: rgba(255, 255, 255, 0.534);
        display: inline-block;
        min-width: 90%;
        padding-top: calc(var(--articleCreate-font-size) * 0.5);
        padding-bottom: calc(var(--articleCreate-font-size) * 0.5);
        user-select: none;
        cursor: pointer;
    }
    #articleCreateBox>#articleCreateBoxRow1>input{
        font-size: calc(var(--articleCreate-font-size) * 0.85);
        text-indent: var(--articleCreate-font-size);
        flex-grow: 0;
        min-width: 90%;
        min-height: calc(2em);
        outline: none;
        border: 1px solid black;
        background-color: rgba(255, 255, 255, 0.932);
        border-radius: 8px;
    }
    #articleCreateBox>#articleCreateBoxRow2{        
        margin-left: 10%;        
    }
    #articleCreateBox>#articleCreateBoxRow2>#articleCreateBoxContent{
        border-radius: 8px;
        font-size: calc(var(--articleCreate-font-size) * 0.85);
        outline: none;
        border: 1px solid black;
        max-width: 90%;
        min-height: calc(10em);
        background-color: rgba(255, 255, 255, 0.945);
        font-size: var(--articleCreate-font-size*0.85);
        padding: calc(var(--articleCreate-font-size) * 0.5);        
        margin-bottom: 5em;
    }
    #articleCreateBox>#articleCreateBoxRow2>span:first-of-type{
        font-size: calc(var(--articleCreate-font-size) * 1.5);
        text-indent: calc(var(--articleCreate-font-size) * 1.5);
        display: inline-block;
        min-width: 90%;
        padding-top: calc(var(--articleCreate-font-size) * 0.5);
        padding-bottom: calc(var(--articleCreate-font-size) * 0.5);
        background-color: rgba(255, 255, 255, 0.479);
        user-select: none;
        cursor: pointer;
    }
    #articleCreateBox>#articleCreateBoxRow3{
        display: flex;
        flex-direction: column;
        position: absolute;
        bottom:10px;
        right:10px;
        align-items: flex-end;
    }   
    #articleCreateBox>#articleCreateBoxRow3>button{
        user-select: none;
        cursor: pointer;
    }
    #articleCreateBox>#articleCreateBoxRow3>button:hover{
        text-shadow: 1px 1px black;
        color: yellow;
    }
    #articleCreateBox>#articleCreateBoxRow3>#articleCreateBoxButton1{

    }
    #articleCreateBox>#articleCreateBoxRow3>#articleCreateBoxButton2{
        margin-top:calc(var(--articleCreate-font-size) * 0.2);;
        margin-bottom:calc(var(--articleCreate-font-size) * 0.2);;
    }
    #articleCreateBox>#articleCreateBoxRow3>#articleCreateBoxButton3{

    }

</style>