<template>
    <article>
        <div id="contextBox" contenteditable="true">
            <p><br /></p>
        </div>
    </article>
</template>

<script>
export default {
    mounted() {
        //this.setDef();
    },
    methods: {
        setDef() {
            let contenteditableBox = document.querySelector("#contextBox");
            let contenteditableBoxFirstP = contenteditableBox.firstChild;
            let range = document.createRange(); // Range 객체 생성-텍스트, 엘리먼트 등의 선택 영역.
            let sel = window.getSelection(); //현재 문ㅅ의 선택 객체 가져오기. 클라이언트가 선택한 텍스트 또는 요소
            range.setStart(contenteditableBoxFirstP, 0); //Node 호출 및, Range의 시작 지점을 설정. 커서를 놓을 요소 및 지점
            range.collapse(true); //Range 축소, 단일 지점으로 축소함.
            sel.removeAllRanges(); //현재 선택한 영역 모두 제거
            sel.addRange(range); //새로운 range를 선택 영역에 추가
        },
    },
};
</script>

<style>
#contextBox {
    position: absolute;
    
    
    top: 50%;
    left: 0;
    color: black;
}
</style>
