class canvasDrawer{

    constructor(canvas){
        if(!(canvas instanceof HTMLCanvasElement)){
            throw new Error('Canvas Error!')
        }
        this.canvas = canvas;
        this.context = this.canvas.getContext('2d')
    };
    clearCanvas(x=0,y=0,width=this.canvas.width,height=this.canvas.height){
        this.context.clearRect(x, y, width, height);
    }
    drawText(x, y, text, font = '16px Arial', color = 'black') {
        this.context.font = font;
        this.context.fillStyle = color;
        this.context.fillText(text, x, y);
    }

    drawImage(x, y, imageUrl, width, height) {
        const image = new Image();
        image.src = imageUrl;
        image.onload = () => {
            this.context.drawImage(image, x, y, width, height);
        };
    }

    drawShape(type, x, y, size, angles = 0,color='black',distortion = 0) {
        //n각형, 위치x,위치y,반지름 또는 면의 길이, 찌그러진 정도(0.1=>10%)
        //save를 해 임시 저장 하고, restore로 save로 되돌아감.
        this.context.beginPath();        
        let angle = (Math.PI / 180) * angles
        if(type>=0 && type<=3){
            switch(type) {
                case 0: // Circle
                    this.context.save();    
                    this.context.fillStyle = color;
                    this.context.ellipse(x, y, size, size * (1 - distortion), 0, 0, Math.PI * 2);
                    this.context.fill();
                    this.context.restore();
                    break;
                case 1: // Point
                    this.context.save();    
                    this.context.fillStyle = color;
                    this.context.fillRect(x, y, 1, 1);
                    this.context.restore();
                    break;
                case 2: // Line
                this.context.save();
                this.context.fillStyle = color;
                this.context.translate(x, y);
                this.context.rotate(angle);
                this.context.moveTo(0, 0);
                this.context.lineTo(size, 0);
                this.context.stroke();
                this.context.restore();
                    break;
                case 3: // Triangle
                    this.drawPolygon(x, y, size, 3, angle, color);
                    break;
                case 4: // Rectangle
                this.context.save();
                this.context.fillStyle = color;
                this.context.translate(x, y);
                this.context.rotate(angle);
                this.context.fillRect(-size / 2, -size * (1 - distortion) / 2, size, size * (1 - distortion));
                this.context.restore();
                    break;                
                default:
                    console.error('Unsupported shape type');
            }
        }else if(type>=4){            
                this.drawPolygon(x, y, size, type,angle,color);
                    
        }else{
            console.error('Unsupported shape type');
        }
        
    }

    drawPolygon(x, y, size, sides,angle,color) {
        const angleStep = (2 * Math.PI) / sides;
        const adjustAngle = (Math.PI / 2)
        this.context.save();
        this.context.fillStyle = color;
        this.context.translate(x, y);
        
            if(sides>=6){
                this.context.rotate(adjustAngle * (4+sides) / (sides * 2) );
            }else if(sides==5){
                this.context.rotate(adjustAngle * (4+2) / (sides * 2) );
            }else if(sides==4){
                this.context.rotate(adjustAngle * (4) / (sides * 2) );
            }else if(sides==3){
                this.context.rotate(adjustAngle * (2) / (sides * 2) );
            }
            this.context.rotate(angle);
        
        this.context.moveTo(size * Math.cos(0), size * Math.sin(0));
        for (let i = 1; i < sides; i++) {
            this.context.lineTo(size * Math.cos(i * angleStep), size * Math.sin(i * angleStep));
        }
        this.context.closePath();
        this.context.fill();
        this.context.restore();
    }
    clearShape(index) {
        if (index < 0 || index >= this.shapes.length) {
            console.error('Invalid shape index');
            return;
        }
        this.shapes.splice(index, 1);
        this.redraw();
    }

    redraw() {
        this.clearCanvas();
        this.shapes.forEach(shape => {
            if (shape.type === 'shape') {
                this.drawShape(shape.shapeType, shape.x, shape.y, shape.size, shape.angles, shape.color, shape.distortion);
            } else if (shape.type === 'image') {
                this.drawImage(shape.x, shape.y, shape.imageUrl, shape.width, shape.height);
            }
        });
    }
}

export default canvasDrawer;