import TokenService from "./token/TokenService";

class naving{

    getNavBar(routes, token){
        let power = 0;
        if(token!=null){
            let newToken = TokenService.decodeToken();
            power = newToken.data.userPower;
        }
        let obj1 = {
            themes : "",
            items : [],
            childView : false
        };
        
        let a = [...routes]
        let b = [], c=[];
        a.forEach(el=>{
            let b1 = {...el};
            b.push(b1)
        })
        
        
        b = b.filter(el => el.theme !== "Main");
        if(power <3){
            b = b.filter(el => el.theme !== "Admin");
        }else{
            b.sort((x, y) => {
                if (x.theme === "Admin") return -1;
                if (y.theme === "Admin") return 1;
                return 0;
            });
        }
        
        b.forEach( (el, index)=>{
            let findIndex = c.findIndex(els=>els.themes == el.theme);            
            if (findIndex== -1){
                
                let tempObj = JSON.parse(JSON.stringify(obj1))
                tempObj.themes = el.theme;
                tempObj.items.push(el)
                c.push(tempObj)
                
                
            }else{
                c[findIndex].items.push(el)
            }
        });
        return this.adjustNavBar(c);
        
    };
    adjustNavBar(arrays){
        let z = true, x1 = 3, x2 = 1;
        let a = 0;
        arrays.forEach( (el, index)=>{            
            if(el.items.length ==1){
                el.Qsingle = true;
            }
            // if(a <=5){
            //     el.Qview = true;
            //     if(z){
            //         el.Qpos = `left${x1}`
            //         x1++;
            //     }else{
            //         el.Qpos = `right${x2}`
            //         x2++;
            //     }
            //     z=!z
            //     a++;
            // }
            if(a <=5){
                el.Qview = true;
                if(x1>=1){
                    el.Qpos = `left${x1}`
                    x1--;
                }else{
                    el.Qpos = `right${x2}`
                    x2++;
                }                
                a++;
            }
            else{
                el.Qview=false;
                el.Qpos=""
            }
        })
        return arrays;
    }
    

}
export default new naving;