<template>    
    <div v-if="Object.keys(articleInfo).length" id="articleInner" v-show="!articleAdjustTF">
        <header id="articleInnerHeader">
            <div id="articleInnerHeaderRow1">
                <span v-html="articleInfo.board.boardName" id="articleInnerHeaderRow1BoardName" @click="() => {
        this.$router.push({name: 'board',params : {no:gotCode[0]} });
    }"></span>
                <span v-html="articleInfo.user.userName" id="articleInnerHeaderRow1UserName"></span>
            </div>
            <div id="articleInnerHeaderRow2">
                <span v-html="articleInfo.articleTitle"></span>
            </div>            
        </header>
        <section id="articleInnerSection" v-html="articleInfo.articleContent"></section>
        <footer id="articleInnerFooter">
            <div id="articleInnerFooterButtons" v-show="tokenData">
                <button v-show="(tokenData.userNo==articleInfo.user.userNo)||(tokenData.userPower>=3)" @click="buttonDelete">삭제</button>
                <button v-show="(tokenData.userNo==articleInfo.user.userNo)||(tokenData.userPower>=3)" @click="buttonAdjust">수정</button>
                <button @click="() => {
        this.$router.push({name: 'board',params : {no:gotCode[0]} });
    }">뒤로</button>                
            </div>            
        </footer>
    </div>    
    <article v-else>
        <div @click="() => {
        this.$router.push({name: 'board',params : {no:gotCode[0]} });
    }
        " style="
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      position: relative;
      left: 0%;
      translate: 0% 200%;
    ">
        <div class="iconDelete" style="background-size: 100px 100px; width: 100px; height: 100px"></div>
        <div style="font-size: 25px">No Data!</div>
    </div>
    </article>
    <articleAdjust
        :articleInfo="articleInfo"
        :boardInfo="articleInfo.board"
        :tokenData="tokenData"
        @informOut="buttonAdjust"
    v-if="articleAdjustTF">

    </articleAdjust>
</template>

<script>
import { mapState, mapActions } from "vuex";
import ArticleService from '@/java/service/ArticleService';
import TokenService from "@/services/token/TokenService";

import articleAdjust from "@/views/underViews/articleAdjust.vue"
export default {
    // 삭제된 친구 아니면 그냥 불러와지게끔하자가 내 논지
    name: 'articleInner',
    components : {
        articleAdjust
    },
    data(){
        return{            
            articleInfo : {},
            tokenData : {},
            articleAdjustTF : false,
        }
    },
    computed: {        
        // gotBoardNo() {
        //     return this.$route.query.a;
        // },
        // gotArticleNo() {
        //     return this.$route.query.b;
        // },
        ...mapState(["token"]),
        signedToken() {            
            return this.token;
        },        
        gotCode(){            
            return (this.$route.params.nums).split('#').map(array => 
                parseInt(array.split('').map(char=> (char.charCodeAt(0) - 97)).join(''))
            ); //[a=0 , z=25] (앞이 보드 뒤가 아티클) 사실 articleInfo로 다 얻으니 상관없긴 해... 보드인포는 안 써도 되잖아?
        }
    },
    watch: {
        signedToken(newToken) {            
            if (newToken === null) {
                this.refreshTokenVuex();
            } else {
                this.tokenData = TokenService.decodeToken().data;                
            }
        },
    },
    mounted() {
        if(this.gotCode!=null){
            this.setDef()
        }
        if (this.signedToken === null) {
            this.refreshTokenVuex();
        } else {
            this.tokenData = TokenService.decodeToken().data;            
        }
    },
    methods: {
        ...mapActions(["refreshTokenVuex", "setPopUpMsg", "setBoardInfo"]),
        async setDef(){
            let res = await ArticleService.readArticle(this.gotCode[1]);
            try {
                if(res.status == 200){
                    this.articleInfo = res.data                    
                } 
            }   catch (error) {
                
            }
        },
        async buttonDelete(){
            let checked = [];
            checked.push(this.gotCode[1])
            const res = await ArticleService.dumpArticles(this.gotCode[0],checked);
            try {                
                console.log("삭제완료")
                this.$router.push({name: 'board',params : {no:this.gotCode[0]} })
            } catch (error) {
                
            }
        },
        buttonAdjust(){
            if(this.articleAdjustTF){
                this.setDef()
            }
            this.articleAdjustTF = !this.articleAdjustTF;

        }
    }

}
</script>

<style>
    :root{
        --article-inner-font-size:16px;

    }
    #articleInner{
        padding: calc(var(--article-inner-font-size) * 3);
        width: 100%;
        min-height: var(--calculatedRouterDivHeight);
        background: rgba(255, 211, 169, 0.774);
        position: relative;
    }
    #articleInnerHeader{
        font-family: 'd2';
        display: flex;
        flex-direction: column;        
    }
    #articleInnerHeader > #articleInnerHeaderRow1{
        display: flex;
        flex-direction: row;        
        justify-content: space-between;
    }
    #articleInnerHeader > #articleInnerHeaderRow1 > #articleInnerHeaderRow1BoardName{
        font-size: calc(var(--article-inner-font-size) * 1.3);
        cursor: pointer;
        user-select: none;
    }
    #articleInnerHeader > #articleInnerHeaderRow1 > #articleInnerHeaderRow1UserName{
        font-size: calc(var(--article-inner-font-size) * 1.1);
        display: inline-flex;
        align-items: center;
    }
    #articleInnerHeader > #articleInnerHeaderRow1 > #articleInnerHeaderRow1UserName::before{
        content:"작성자 : ";
        font-size: calc(var(--article-inner-font-size) * 0.9);
        
    }
    #articleInnerHeaderRow2{
        display: inline-flex;
        justify-content: center;
        margin-top: calc(var(--article-inner-font-size) * 0.9);
        margin-bottom: calc(var(--article-inner-font-size) * 1.9);
        font-size: calc(var(--article-inner-font-size) * 1.5);
    }
    #articleInnerSection{
        font-family: 'ridi';
        font-size: calc(var(--article-inner-font-size) * 1);
    }
    #articleInnerFooter{
        font-family: 'dohyeon';
        position: absolute;
        bottom: calc(var(--article-inner-font-size) * 2);
        right: calc(var(--article-inner-font-size) * 2);
    }
    #articleInnerFooter>#articleInnerFooterButtons{
        display: flex;

    }
    #articleInnerFooter>#articleInnerFooterButtons>button{
        display: block;
        background-color: black;
        color:beige;
        font-size: calc(var(--article-inner-font-size) * 1.1);
        padding: calc(var(--article-inner-font-size) * 0.2);;
        line-height: calc(var(--article-inner-font-size) * 1.5);;
        text-align: center;
        margin-left: calc(var(--article-inner-font-size) * 0.5);
        border-radius: calc(var(--article-inner-font-size) * 0.6);;
        cursor: pointer;
    }
    #articleInnerFooter>#articleInnerFooterButtons>button:hover{
        color:yellow;
    }
</style>