import {jwtDecode} from 'jwt-decode';


class TokenService{
    
    constructor(){
        this.result = {
            status : 100,
            data : null
        }
    }
    getToken(){
        const token = localStorage.getItem('token')
        if(token!=null){
            const decodeToken = jwtDecode(token);
            const currentTime = Date.now() / 1000;
            if(decodeToken.exp < currentTime){
                return null    
            }else{
                return token
            }
            
        }
        //console.log('you got no token',token);
        return null;
    }
    setNewToken(data){
        localStorage.setItem('token',JSON.stringify(data.data.token));        
        return this.decodeToken();
    }
    decodeToken(){
        let newResult = JSON.parse(JSON.stringify(this.result));
        const currentTime = Date.now() / 1000;
        const token = localStorage.getItem('token')        
        if(token !=null){
            const decodeToken = jwtDecode(token);
                if(decodeToken.exp < currentTime){
                newResult.status = 400;
                return newResult
                }
            newResult.status = 200;
            newResult.data = decodeToken;
            return newResult
        }else{
            newResult.status = 400;
            return newResult
        }
        
    }
    removeToken(){
        let newResult = JSON.parse(JSON.stringify(this.result));
        if(localStorage.getItem('token') != null){
            localStorage.removeItem('token')
            newResult.status = 202;//HttpStatus.ACCEPTED
            return newResult;
        }else{
            newResult.status = 400;
            return newResult;
        }
    }

}

export default new TokenService();