<template>
    <div id="game21">
        <canvas id="gameCanvas" width="800" height="600"></canvas>
        <div id="gameButtons">
            <button type="button" class="game21_buttons" @click="startGame" v-show="!started">start</button> 
            <button type="button" class="game21_buttons" @click="hit" v-show="started">hit</button>
            <button type="button" class="game21_buttons" @click="stand" v-show="started">stand</button>
        </div>
    </div>
</template>

<script>
import game21 from "@/extraservices/game21.js"
export default {
    data () {
        return{
            canvasSize: {
                w: null,
                h: null,
            },
            game: null,
            context: null,
            player : [],
            dealer : [],
            started : false,
        }
    },
    mounted (){
        this.initial();
        
    },
    methods: {
        initial() {            
            this.game = new game21(1);
            this.checkWidth();
        },
        checkWidth() {
            const canvas = document.getElementById("gameCanvas");
            this.context = canvas.getContext("2d");
            console.log(canvas)
            const canvasStyle = window.getComputedStyle(document.getElementById('gameCanvas'));
            this.canvasSize.w = parseInt(canvasStyle.width, 10);
            this.canvasSize.h = parseInt(canvasStyle.height, 10);
            
        },
        startGame() {
            this.game.dealInitialCards();
            this.started = true;
            this.drawCards(0);
        },
        drawCards(index){
            this.game.playerHand[index].x = this.game.playerHand[0].width *(index + 1) - (15 / 800) * this.canvasSize.w + (30 / 800) * this.canvasSize.w *(index );
            this.game.playerHand[index].y = this.canvasSize.h - this.game.playerHand[0].height - (30 / 600) * this.canvasSize.h;
            this.game.playerHand[index].size = this.canvasSize;            
            this.game.playerHand[index].draw(this.context);
            this.game.dealerHand[index].x = this.game.dealerHand[0].width *(index + 1) - (15 / 800) * this.canvasSize.w + (30 / 800) * this.canvasSize.w *(index);
                        this.game.dealerHand[index].y = this.game.dealerHand[0].height - (30 / 600) * this.canvasSize.h;
            this.game.dealerHand[index].size = this.canvasSize;            
            this.game.dealerHand[index].fold=true;
            this.game.dealerHand[index].draw(this.context);
            this.player.push(this.game.playerHand[index]);
            this.dealer.push(this.game.dealerHand[index]);
            console.log(this.player, this.dealer)
        },
        hit(){
            this.checkCards(this.player);
            this.checkCards(this.dealer);
        },
        checkCards(player){
            let value = 0;
            for(let card of player){
                value += card.value
            }
            console.log(value)
        }
    },
}
</script>

<style>
body.body-game21 #routerDiv {
    background-color: transparent;
}
body.body-game21{
    background-image: url("@/assets/background/Wallpaper_03.png");
}
nav.nav-game21 #navMain i{
    color:purple
}
nav.nav-game21 .navDiv{
    border: 3px solid red;
}
nav.nav-game21 .navDiv>a{
    color:red;
}
nav.nav-game21 .navDiv .navArrow{
    color:red;
}
#game21{
    display: flex;
    width: 100%;
    height: var(--calculatedRouterDivHeight);
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#gameCanvas{
    background-image: url("@/assets/background/Wallpaper_08.png");    
    display: block;
    flex-grow: 0;
    flex-shrink: 1;    
    border-radius: 15px;
    aspect-ratio: 800 / 600;
    width : min(800px, 100%);
}
#gameButtons{
    display:flex;
    margin: 15px;
    flex-direction: row;
}
.game21_buttons{
    display: block;
    background: whitesmoke;
    padding:1em;
    font-family: 'pokers';
    border-radius: 10px;
    box-shadow: 1px 1px 5px 2px black;
    cursor: pointer;
    margin-right:13px;
}
</style>