<template>
    <table id="signin-box">
        <tr id="signin-box-tr-canvas">
            <td>
                <canvas id="signin-box-canvas" width="600" height="400">

                </canvas>
            </td>
        </tr>
        <tr>
            <td><input type="text" v-model="userInfo" id="userInfoInput" v-if="!logged.TF"/><button type="button" @click="submitUserInfo" v-if="!logged.TF">입력</button><button type="button" @click="moveToSignOn">회원가입</button></td>
            
        </tr>
        <tr>
            <p @click="signOut">sdfasdf</p>
            <td>{{ userId }} {{ userPwd }}</td>
        </tr>
    </table>
</template>

<script>
import cd from '@/services/canvasDrawer.js';
import DataService from '@/java/service/DataService.js'
import TokenService from '@/services/token/TokenService';
import {mapActions, mapState} from 'vuex';
import {jwtDecode} from 'jwt-decode';
export default {
    data(){
        return{
            logged : {
                id : null,
                TF : false
            },
            canvas : null,
            drawer : null,
            userInfo:'',
            userId : '',
            userPwd : '',
            user : {
                userId : '',
                userPwd : ''
            }
        }
    },
    watch:{
        
    },
    created(){
        this.checkToken();
    },
    
    mounted(){        
        this.setDef()
    },
    methods : {
        ...mapActions(['signIn','signOut']),
        checkToken(){
            let userToken = TokenService.decodeToken();
            if(userToken.status == 200){
            this.logged.id = userToken.data.sub;            
            this.logged.TF = true;
        }
        },
        setDef(){
            this.checkToken();
            this.userInfo=''; this.userId = ''; this.userPwd='';
            this.canvas = document.getElementById('signin-box-canvas');
            this.drawer = new cd(this.canvas);
            //drawShape(n각형, x, y, 변 길이(반지름), !회전각, !색, !찌그러진정도 )
            //drawText(x,y,!font설정,!색)
            this.drawer.drawShape(0,100,100,30,180,'blue',0.3)
            
            this.drawer.drawShape(10,500,100,30,180,'green')
            this.drawer.drawShape(4,200,100,10,0,'yellow')
            this.drawer.drawShape(5,300,100,30)
            this.drawer.drawShape(3,300,200,30,0)
            if(!this.logged.TF){
                this.drawer.drawText(50, 50, '아이디를 입력해주세요', '20px Arial', 'blue');
            }else{
                this.drawer.drawText(50, 50, `${this.logged.id}! 안녕하세요`, '30px Arial', 'blue');
            }
            
            this.drawer.drawText(50, 100, '나중에 이쁘게 하자 여기는', '20px Arial', 'blue');
        },
        submitUserInfo(){
            this.drawer.clearCanvas();
            if((this.userInfo == null || this.userInfo == '')&&(this.userId==''||this.userId==null)){                
                this.drawer.drawText(50, 50, '아이디를 제대로 입력해주세요', '20px Arial', 'yellow');
                return;
            }else if((this.userInfo == null || this.userInfo == '')&&(this.userPwd==''||this.userPwd==null)){
                this.drawer.drawText(50, 50, '비밀번호를 제대로 입력해주세요', '20px Arial', 'yellow');
                return;
            }
            if(this.userId==null||this.userId==''){
                this.drawer.drawText(50, 50, '비밀번호를 입력해주세요', '20px Arial', 'yellow');
                this.userId = this.userInfo;
                this.userInfo = null
            }else if(this.userPwd==null||this.userPwd==''){
                this.drawer.drawText(50, 50, '우선 입력 성공', '20px Arial', 'yellow');
                this.userPwd = this.userInfo;
                this.userInfo = null
                this.user.userId = this.userId;
                this.user.userPwd = this.userPwd;
                DataService.signIn(this.user)
                    .then(res=>{
                        if(res.status == 200){                        
                        this.signIn(res.data)
                        this.logged.id = res.data.sub;
                        this.logged.TF = true;
                        this.drawer.clearCanvas();
                        this.drawer.drawText(50, 50, `${this.logged.id}! 안녕하세요`, '30px Arial', 'blue');
                        location.reload()
                    }
                    })
                    .catch(e=>{
                        console.error(e)  
                    })
            }
        },
        moveToSignOn(){
            this.setDef();
            this.$router.push({path:"/sign-on"})
        },        

    }
}
</script>

<style>
#signin-box{
    box-shadow: 5px 5px 5px black;
    position: absolute;
    top:50%;
    left:50%;
    transform: translateX(-50%) translateY(-50%);
    max-width: 800px;
    max-height: 600px;
}
#singin-box, #signin-box tr, #signin-box td{
    border-spacing: 0px;
    box-sizing: border-box;
    padding:0;
    border-collapse: collapse;
}
#signin-box #signin-box-tr-canvas{
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(231, 225, 225);   
}
</style>